import { ACCORDION_CUSTOM_EVENT_NAMES } from '../../../../webcomponents/va-accordion/custom-event-names';
import { VaAccordionItem } from '../../../../webcomponents/va-accordion/va-accordion-item';
import { GoogleTagManagerService } from './google-tag-manager-service';
import { SLIDESHOW_CUSTOM_EVENT_NAMES } from '../../../../webcomponents/va-slideshow/custom-event-names';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';

const ContentViewsClassName = 'js-gtm-content-view';
const SlideShowTitleClassName = 'js-gtm-slideshow-title-label';

/**
 * Generic element click event handler for GTM Content Views event
 * @param evt - Event arguments
 */
function clickHandler(evt: Event) {
    const anchor = evt.currentTarget as HTMLAnchorElement;
    GoogleTagManagerService.getInstance().pushContentViewsEvent(anchor.dataset.gtmLabel || anchor.innerText, anchor.href);
}

/**
 * Generic slideshow slide changed event handler for GTM Content Views event
 * @param evt - Event arguments
 */
function slideChanged(evt: CustomEvent) {
    if (!evt.detail.automatic) {
        GoogleTagManagerService.getInstance().pushContentViewsEvent(evt.detail.title, window.location.href);
    }
}

/**
 * Slideshow slide changed event handler that pushes GTM Content Views event with the title on the label
 * @param evt - Event arguments
 */
function slideChangedTitleLabel(evt: CustomEvent): void {
    if (!evt.detail.automatic) {
        GoogleTagManagerService.getInstance().pushEvent(GTMEvent.ContentViews, GTMCategory.ContentEngagement, GTMEvent.ContentViews, evt.detail.title);
    }
}

/**
 * Accordion click event handler for GTM Content Views event
 * @param evt - Event arguments
 */
function itemExpandHandler(evt: CustomEvent) {
    if (evt.detail.isExpanded) {
        GoogleTagManagerService.getInstance().pushContentViewsEvent(evt.detail.title, window.location.href);
    }
}

/**
 * Tracks content view events
 */
export function trackContentViews(): void {
    const anchorElements = document.querySelectorAll<HTMLAnchorElement>(`a.${ContentViewsClassName}`);
    anchorElements.forEach(element => element.addEventListener('click', clickHandler));

    const accordionElements = document.querySelectorAll<VaAccordionItem>('va-accordion-item');
    accordionElements.forEach(item => item.addEventListener(ACCORDION_CUSTOM_EVENT_NAMES.ItemClick, itemExpandHandler));

    const slideShows = document.querySelectorAll(`va-slideshow.${ContentViewsClassName}`);
    slideShows.forEach(element => element.addEventListener(SLIDESHOW_CUSTOM_EVENT_NAMES.SlideChanged, element.classList.contains(SlideShowTitleClassName) ? slideChangedTitleLabel : slideChanged));
}
