import { GoogleTagManagerService, emptyLabel } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { ExtendedWindow } from '../../models/extended-window.model';

declare const window: ExtendedWindow;

const gtmService = GoogleTagManagerService.getInstance();

/**
 * Registers event callbacks for tracking Address Change events
 */
export function trackAddressChangeForm(): void {
    const addressChangeFormElement = document.querySelector('.va-address-change-form');

    if (addressChangeFormElement) {
        gtmService.pushEvent(GTMEvent.AddressChange, GTMCategory.ClientEnquiries, 'Address change: start', emptyLabel);

        addressChangeFormElement.addEventListener('submit', () => {
            if (!window.grecaptcha || window.grecaptcha.getResponse()) {
                gtmService.pushEvent(GTMEvent.AddressChange, GTMCategory.ClientEnquiries, 'Address change: complete', emptyLabel);
            }
        });
    }
}
