import { parse, isBefore, isAfter, isSameDay } from 'date-fns';

/**
 * The default date format used for date parsing and formatting
 */
export const defaultDateFormat = 'dd.MM.yyyy';

/**
 * Convert a date string to a date with a given format
 * @param date - the date string to convert
 * @param format - the format of the date string
 * @returns the date object converted from the string
 */
export function dateStringToDate(date: string | Date, format: string = defaultDateFormat): Date {
    if (date instanceof Date) {
        return date;
    }
    return parse(date, format, Date.now());
}

/**
 * Check if a passed date string is a valid date.
 * @param date - the date string to validate
 * @param format - the format of the date string
 * @returns a boolean indicating whether the date is valid or not
 */
export function isDateValidator(date: string, format: string = defaultDateFormat): boolean {
    const dateObj = dateStringToDate(date, format);
    return !isNaN(dateObj.getTime());
}

/**
 * Check if a given date is in a given range (max values are considered in range)
 * @param dateParam - the date string to validate
 * @param minDateParam - the minimal accepted date
 * @param maxDateParam - the maximal accepted date
 * @param format - the format of the date string
 * @returns a boolean whether the date is in range or not
 */
export function isDateInRangeValidator(dateParam: string | Date, minDateParam: string | Date, maxDateParam: string | Date, format: string = defaultDateFormat): boolean {
    const date = dateStringToDate(dateParam, format);
    const minDate = dateStringToDate(minDateParam, format);
    const maxDate = dateStringToDate(maxDateParam, format);

    const isInRange = isAfter(date, minDate) && isBefore(date, maxDate);
    const isEqualALimit = isSameDay(date, minDate) || isSameDay(date, maxDate);

    return isInRange || isEqualALimit;
}
