import { VaButton } from '../../../../../webcomponents/va-button/va-button';
import { VaFlip } from '../../../../../webcomponents/va-flip/va-flip';
import { VaSlider } from '../../../../../webcomponents/va-slider/va-slider';

/**
 * The id of the panel that flips from the calculator to the result
 * @type string
 */
const lifeCalculatorFlipId = 'life-calculator-flip-panel';

/**
 * The id of the element where the monthly premium has to be inserted
 * @type string
 */
const lifeCalculatorMontlyPremiumFieldId = 'life-calculator-premium-monthly';

/**
 * The id of the element where the yearly premium has to be inserted
 */
const lifeCalculatorYearlyPremiumFieldId = 'life-calculator-premium-yearly';

/**
 * The id of the submit btn (on the form page)
 * @type string
 */
const lifeCalculatorSubmitBtnId = 'life-calculator-submit-btn';

/**
 * The id of the back btn (on the result page)
 * @type string
 */
const lifeCalculatorBackBtnId = 'life-calculator-back-btn';

/**
 * The id of the birthdate year input
 * @type string
 */
const birthdateYearId = 'BirthdateYear';

/**
 * The id of the contract duration input
 * @type string
 */
const contractDurationId = 'ContractDuration';

/**
 * The id of the contract duration slider
 * @type string
 */
const contractDurationSliderId = 'ContractDuration-slider';

/**
 * Function that switch from the form to the result panel on the life calculator
 * by using the flip component
 */
function lifeCalculatorSwitchPanel(): void {
    const flipPanel = document.getElementById(lifeCalculatorFlipId) as VaFlip;
    if (flipPanel) {
        flipPanel.flip();
    }
}

/**
 * Private method reused to get life calculator va-flip instance and reset its height (due to container resizing)
 */
function lifeCalculatorSetHeight(): void {
    const flipPanel = document.getElementById(lifeCalculatorFlipId) as VaFlip;
    if (flipPanel) {
        flipPanel.updateContainerSize();
    }
}

/**
 * Private method reused to get life calculator submit button and reseting its state
 */
function lifeCalculatorSubmitButtonResetState(): void {
    const submitButton = document.getElementById(lifeCalculatorSubmitBtnId) as VaButton;
    if (submitButton) {
        submitButton.toggleLoadingState(false);
    }
}

/**
 * Callback method triggered on click on the back button of the form result page
 * @param _event - The submit event
 */
function lifeCalculatorFlipIdBackBtnOnClick(_event: Event): void {
    lifeCalculatorSwitchPanel();
}

/**
 * This is the method called by the ajax promise when the life calculator webservice
 * was responded successfully
 * @param yearlyValue - the value calculated by the webservice
 */
export function lifeCalculatorSuccess(yearlyValue: number): void {
    const monthlyField = document.getElementById(lifeCalculatorMontlyPremiumFieldId);
    const yearlyField = document.getElementById(lifeCalculatorYearlyPremiumFieldId);

    if (monthlyField) {
        const monthlyValue = yearlyValue / 12;
        monthlyField.innerHTML = monthlyValue.toFixed(2);
    }

    if (yearlyField) {
        yearlyField.innerHTML = yearlyValue.toFixed(2);
    }

    lifeCalculatorSwitchPanel();

    const backBtn = document.getElementById(lifeCalculatorBackBtnId);
    backBtn.addEventListener('click', lifeCalculatorFlipIdBackBtnOnClick);
}

/**
 * This is the method called by the ajax promise when the life calculator webservice was requested
 */
export function lifeCalculatorFinally(): void {
    lifeCalculatorSetHeight();
    lifeCalculatorSubmitButtonResetState();
}

/**
 * This is the method called by the life calculator form when validation occurs
 * @param element - The element on where the validation was triggered
 */
export function lifeCalculatorValidation(element: HTMLInputElement): void {
    lifeCalculatorSetHeight();
    lifeCalculatorSubmitButtonResetState();

    if (element.id === birthdateYearId) {
        const flipPanel = document.getElementById(lifeCalculatorFlipId) as VaFlip;
        const contractDurationInput = flipPanel.querySelector<HTMLInputElement>(`#${contractDurationId}`);
        const contractDurationSlider = flipPanel.querySelector<VaSlider>(`#${contractDurationSliderId}`);

        if (element.validity.valid && contractDurationInput && contractDurationSlider) {
            const age = new Date().getFullYear() - parseInt(element.value, 10);
            const newMax = parseInt(element.dataset.retirementAge, 10) - age;
            contractDurationSlider.max = newMax;

            contractDurationInput.value = newMax.toString();
            contractDurationInput.dispatchEvent(new Event('input'));
        }
    }
}

