
import { PromiseCallbacks } from '../../types';
import { formCallbacks, FormCallbacks } from './callbacks/form-callbacks';
import { classNames } from '../../constants/class-names';

/**
 * The class to identify the form error container
 */
const FORM_ERROR_CONTAINER_SELECTOR = 'js-form-request-error';

/**
 * Method to show / hide the form request generic error
 * @param formElement - the form element
 * @param isShown - the status to force the toggle to a predefined state
 */
function toggleFormRequestError(formElement: HTMLFormElement, isShown?: boolean): void {
    const errorElement = formElement.querySelector(`.${FORM_ERROR_CONTAINER_SELECTOR}`);
    errorElement?.classList?.toggle(classNames.isHidden, !isShown);
}

/**
 * Prepare the http request by fetching request options and getting field values
 * @param formElement - the form element
 * @returns a Request object containing all options for the http request
 */
function prepareRequest(formElement: HTMLFormElement): Request {
    const methodWithoutBody = ['GET', 'HEAD', 'DELETE'];
    const contentType = 'application/json';
    const method = formElement.method;

    const body: {[key: string]: string} = {};
    if (!methodWithoutBody.includes(method.toUpperCase())) {
        const formData = new FormData(formElement);
        // eslint-disable-next-line no-extra-parens
        formData.forEach((value: string, key: string) => (body[key] = value));
    }

    return new Request(formElement.action, {
        method,
        headers: { 'Content-Type': contentType },
        body: JSON.stringify(body)
    });
}

/**
 * Define and prepare the callbacks from the form tag data attributes
 * @param formName - the name property of the form element
 * @returns the object containing the callbacks
 */
function prepareCallbacks(formName: string): PromiseCallbacks {
    const successCbName = formName + 'Success' as keyof FormCallbacks;
    const errorCbName = formName + 'Error' as keyof FormCallbacks;
    const finallyCbName = formName + 'Finally' as keyof FormCallbacks;

    const success = formCallbacks[successCbName];
    const rejection = formCallbacks[errorCbName];
    const finallyCb = formCallbacks[finallyCbName];

    return { success, rejection, finally: finallyCb };
}

/**
 * Prepare and execute the http request
 * @param formElement - the form element
 */
export function makeHttpRequest(formElement: HTMLFormElement) {
    const request = prepareRequest(formElement);
    const callbacks = prepareCallbacks(formElement.name);
    toggleFormRequestError(formElement, false);
    fetch(request)
        .then(response => {
            if (!response.ok) {
                throw Error(response.statusText);
            }
            return response.json();
        })
        .then(callbacks.success)
        .catch(error => {
            toggleFormRequestError(formElement, true);

            if (callbacks.rejection) {
                callbacks.rejection(error);
            }
        })
        .finally(callbacks.finally);
}
