import Style from './va-slider.scss';
import { LitElement, html, property, customElement, css, unsafeCSS, TemplateResult } from 'lit-element';
import { SLIDER_CUSTOM_EVENT_NAMES } from './custom-event-names';

/**
 * The tagname of the current component
 */
export const COMP_NAME = 'va-slider';

/**
 * The selector corresponding to the runnable track of the slider, the section that is filled
 */
const LEFT_SIDE_SELECTOR = '.left-side';

/**
 * The selector corresponding to the slier track itself, the section that is not filled
 */
const RIGHT_SIDE_SELECTOR = '.right-side';

/**
 * The default color to the runnable left track of the slider ($color--primary var on SASS files), the section that is filled
 */
const LEFT_SIDE_DEFAULT_COLOR = '#009640';

/**
 * The default color to the runnable right track of the slider ($color--greyscale-300 var on SASS files), the section that is not filled
 */
const RIGHT_SIDE_DEFAULT_COLOR = '#d5d5d5';

/**
 * A component to represent the slider input
 */
@customElement(COMP_NAME)
export class VaSlider extends LitElement {
    @property({ type: Boolean })
    public isDisabled = false;

    @property({ type: Number })
    public min = 0;

    @property({ type: Number })
    public max = 100;

    @property({ type: Number })
    public step = 1;

    @property({ type: Number })
    public value = 0;

    @property({ type: Number })
    public tabindex = 0;

    public sliderChanged(event: Event): void {
        const target = event?.target as HTMLInputElement;
        const value = +target?.value || 0;

        this.updateSliderLeftColor(value);

        this.dispatchEvent(new CustomEvent(SLIDER_CUSTOM_EVENT_NAMES.SliderValueChanged, {
            bubbles: true,
            cancelable: true,
            composed: true,
            detail: { source: this.id, value }
        }));
    }

    /**
     * Component lifecycle method
     * @see https://lit-element.polymer-project.org/guide/lifecycle#updated
     */
    public updated(): void {
        this.updateSliderLeftColor(this.value);
    }

    private updateSliderLeftColor(selectedValue: number): void {
        const range = this.max - this.min;
        const percentage = range > 0 ? (selectedValue - this.min) / range * 100 : 0;
        const inputElement = this.shadowRoot.querySelector('input');

        if (inputElement) {
            const leftSideColor = this.getStylePropertyFromClass(LEFT_SIDE_SELECTOR, 'color') || LEFT_SIDE_DEFAULT_COLOR;
            const rightSideColor = this.getStylePropertyFromClass(RIGHT_SIDE_SELECTOR, 'color') || RIGHT_SIDE_DEFAULT_COLOR;

            inputElement.setAttribute('style', `background: linear-gradient(to right, ${leftSideColor} 0%, ${leftSideColor} ${percentage}%, ${rightSideColor} ${percentage}%, ${rightSideColor} 100%)`);
        }
    }

    private getStylePropertyFromClass(className: string, cssProperty: string): string {
        const cssRulesList = Array.from(Style.styleSheet?.cssRules || []);

        if (!cssRulesList.length) {
            return '';
        }

        const cssRule = <CSSStyleRule>cssRulesList.find((rule: CSSStyleRule) => rule.selectorText?.indexOf(className) > 0);

        if (!cssRule) {
            return '';
        }

        return cssRule.style.getPropertyValue(cssProperty);
    }

    /**
     * @override
     */
    render(): TemplateResult {
        return html`<input id="${this.id}"
                        type="range"
                        min="${this.min}"
                        max="${this.max}"
                        step="${this.step}"
                        tabindex="${this.tabindex}"
                        .value="${this.value}"
                        ?disabled="${this.isDisabled}"
                        aria-label="${this.getAttribute('aria-label') || ''}"
                        @change="${this.sliderChanged}"
                        @input="${this.sliderChanged}">`;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
