import { LitElement, property, css, unsafeCSS, query, html } from 'lit-element';
import { AnimationSettings } from './animation-settings';
import { SLIDESHOW_CUSTOM_EVENT_NAMES } from './custom-event-names';
import Style from './slideshow-item.scss';

export const CLASS_NAMES = {
    isActive: 'is-active',
    isAnimated: 'is-animating'
};

export const DEFAULT_SLIDE_ANIMATION_DURATION = 3000;

const SLIDE_QUERY_SELECTOR = 'div.carousel__item';

/**
 * Slideshow item component base class.
 */
export abstract class SlideshowItem extends LitElement {

    @property({ type: Boolean })
    public isActive = false;

    @property({ type: String })
    public mediaSource = '';

    @property({ type: String })
    public mediaSourceAlt = '';

    @property({ type: String })
    public mediaSourceMd = '';

    @query(SLIDE_QUERY_SELECTOR)
    protected _slide!: HTMLElement;

    /**
    * Gets or sets slide title
    */
    @property({ type: String })
    public title = '';

    /**
     * Slide animation instance.
     * */
    private _animation: Animation;

    /**
     * Initializes the slide item.
     */
    public initializeSlide(): void {
        this._slide.classList.toggle(CLASS_NAMES.isActive, this.isActive);
    }

    /**
     * Handler for the animation finish event
     * @param animation Animation instance
     */
    protected onFinishShowAnimationHandler(animation: Animation): void {
        this._slide.classList.remove(CLASS_NAMES.isAnimated);
    }

    /**
     * Shows the slide item with the required animation.
     */
    public show(animationSettings: AnimationSettings): void {
        if (this._slide.animate) {
            if (!this.isRunningAnimation()) {
                this._animation = this._slide.animate(animationSettings.showKeyFrames, animationSettings.options);

                this._animation.ready.then((value: Animation) => {
                    this._slide.classList.add(CLASS_NAMES.isActive, CLASS_NAMES.isAnimated);
                });

                this._animation.finished.then((value: Animation) => {
                    this.onFinishShowAnimationHandler(value)
                });
            }
        } else {
            this._slide.classList.add(CLASS_NAMES.isActive);
            this.onFinishShowAnimationHandler(null);
        }
    }

    /**
     * Hides the slide item with the speficied animation.
     */
    public hide(animationSettings: AnimationSettings): void {
        if (this._slide.animate) {
            if (!this.isRunningAnimation()) {
                this._animation = this._slide.animate(animationSettings.hideKeyFrames, animationSettings.options);

                this._animation.finished.then(() => {
                    this._slide.classList.remove(CLASS_NAMES.isActive);
                });
            }
        } else {
            this._slide.classList.remove(CLASS_NAMES.isActive);
        }
    }

    /**
     * Dispatch event to set slide animation timeout.
     * @param slideAnimationTimeout slide animation timeout (in milliseconds)
     */
    protected dispatchSetSlideAnimationTimerEvent(slideAnimationTimeout: number): void {
        this.dispatchEvent(new CustomEvent(SLIDESHOW_CUSTOM_EVENT_NAMES.SetSlideAnimationTimer, {
            bubbles: true,
            cancelable: true,
            composed: true,
            detail: { duration: slideAnimationTimeout }
        }));
    }

    /**
     * Swiped right event handler. Only change slide if the swiped element was indeed the slide.
     * @param event custom swipe event
     */
    private swipedToPreviousSlide(event: CustomEvent): void {
        if (event.target === event.detail.swipedElement) {
            this.dispatchEvent(new CustomEvent(SLIDESHOW_CUSTOM_EVENT_NAMES.ShowPreviousSlide, {
                bubbles: true,
                cancelable: true,
                composed: true
            }));
        }
    }

    /**
     * Swiped left event handler. Only change slide if the swiped element was indeed the slide.
     * @param event custom swipe event
     */
    private swipedToNextSlide(event: CustomEvent): void {
        if (event.target === event.detail.swipedElement) {
            this.dispatchEvent(new CustomEvent(SLIDESHOW_CUSTOM_EVENT_NAMES.ShowNextSlide, {
                bubbles: true,
                cancelable: true,
                composed: true
            }));
        }
    }

    /**
     * Checks if this slide is being animated
     */
    public isRunningAnimation(): Boolean {
        return this._animation && this._animation.playState === 'running';
    }

    /**
     * Gets slide show item height
     */
    public getHeight(): number {
        return this._slide.clientHeight;
    }

    firstUpdated() {
        this.initializeSlide();
        this.addEventListener('swiped-right', this.swipedToPreviousSlide);
        this.addEventListener('swiped-left', this.swipedToNextSlide);
    }

    render() {
        return html`
        <div class="carousel__item">
            <slot></slot>
        </div>
        `;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
