import Style from './va-modal.scss';
import { LitElement, html, property, customElement, css, unsafeCSS, TemplateResult, query } from 'lit-element';

/**
 * The tagname of the current component
 */
export const COMP_NAME = 'va-modal';

/**
 * The attribute used on elements to register them as modal trigger.
 * The value of this attribute has to be the id of the current modal (without # prefix)
 */
export const TRIGGER_ATTR = 'data-modal-target';

export const CONTENT_SLOT_NAME = 'content';
export const FOOTER_SLOT_NAME = 'footer';

/**
 * A modal element that will be placed above any content to serve
 * as a dialog box. When used, this modal HAS to have an id attribute defined.
 * Any element of the document that has the trigger attribute, will be
 * extended with an eventListener to trigger the modal (@see TRIGGER_ATTR)
 */
@customElement(COMP_NAME)
export class VaModal extends LitElement {
    /**
     * A cache of the triggers binded to the current component
     */
    private triggerList: Array<Element> = [];

    /**
     * The title that will be placed in the modal header
     */
    @property({ type: String })
    public modalTitle = '';

    /**
     * Some Sitefinity widgets are not very customisable, except for css class modification.
     * For those elements, we allow a custom selector on a trigger to open the current modal.
     * This element is the selector (class, attr, ...) setted on the other element to add the
     * eventlistener that open the current modal. If using the component directly, prefer
     * using the TRIGGER_ATTR instead.
     */
    @property({ type: String })
    public bindableSelector = '';

    /**
     * Property to configure if a click on the backdrop element should close the modal.
     */
    @property({ type: Boolean })
    public closeOnBackdropClick = false;

    /**
     * Additional css classes.
     */
    @property({ type: String })
    public cssClasses = '';

    /**
     * The property informing if the modal is currently displayed (open)
     * Don't change this value directly. Use the helper methods provided in this
     * component
     */
    @property({ type: String })
    private isOpen = false;

    /**
     * The footer slot element.
     */
    @query(`slot[name="${FOOTER_SLOT_NAME}"]`)
    private footer: HTMLSlotElement;

    /**
     * Lifecycle callback when connected to the dom
     * @see https://lit-element.polymer-project.org/guide/lifecycle
     */
    connectedCallback(): void {
        super.connectedCallback();
        this._bindToTrigger();
    }

    /**
     * Lifecycle callback when disconnected from the dom
     * @see https://lit-element.polymer-project.org/guide/lifecycle
     */
    disconnectedCallback(): void {
        super.disconnectedCallback();
        this._unbindFromTrigger();
    }

    /**
     * This method will look for any trigger element
     * to add them an event listener to open this modal.
     */
    private _bindToTrigger(): void {
        if (!this.id && !this.bindableSelector) {
            return;
        }

        if (this.id) {
            this.triggerList = this.triggerList.concat(...document.querySelectorAll(`[${TRIGGER_ATTR}=${this.id}]`));
        }
        if (this.bindableSelector) {
            this.triggerList = this.triggerList.concat(...document.querySelectorAll(`${this.bindableSelector}`));
        }

        this.triggerList.forEach(
            (el: HTMLElement) => el.addEventListener('click', this.show.bind(this), false)
        );
    }

    /**
     * Unbind all eventlistener added to triggers on the page
     */
    private _unbindFromTrigger(): void {
        if (!this.triggerList) {
            return;
        }

        this.triggerList.forEach(
            (el: HTMLElement) => el.removeEventListener('click', this.show, false)
        );
    }

    /**
     * Close the current modal
     * @param evt - the event passed when the action is triggered
     */
    close(evt?: Event): void {
        if (evt && evt.preventDefault) {
            evt.preventDefault();
        }
        this.toggleModal(false);
    }

    /**
     * Show the current modal
     * @param evt - the event passed when the action is triggered
     */
    show(evt?: Event): void {
        if (evt && evt.preventDefault) {
            evt.preventDefault();
        }
        this.toggleModal(true);
    }

    /**
     * Toggle the display of the current modal
     * @param shouldShow - force the toggle to a specific value;
     */
    toggleModal(shouldShow?: boolean): void {
        if (typeof shouldShow !== 'boolean') {
            shouldShow = !this.isOpen;
        }

        this.isOpen = shouldShow;
        // freeze the body to remove the scroll bar for the background when modal is displayed
        document.querySelector('body').style.overflow = shouldShow ? 'hidden' : '';
    }

    /**
     * Event handler for the modal backdrop element clicks.
     * @param event - event passed when the action is triggered
     */
    private backdropClickHandler(event: Event) : void {
        const eventTarget = event.target as HTMLElement;

        if (this.closeOnBackdropClick && eventTarget.matches('.modal,.modal__dialog')) {
            this.close();
        }
    }

    /**
     * Checks if the footer should be displayed (displays only if at least one node is on the slot)
     * @returns true if should display footer, otherwise false.
     */
    get displayFooter() : boolean {
        return this.footer?.assignedNodes().length > 0;
    }

    /**
     * Renders the modal title element. If title is empty, no element is added.
     * @returns modal title template
     */
    get renderTitle() : TemplateResult {
        return this.modalTitle !== '' ?
            html`<h1 class="modal__title" id="title-${this.id}">${this.modalTitle}</h1>` :
            html``;
    }

    /**
     * @override
     */
    render() {
        return html`
            <div class="modal ${this.isOpen ? 'is-modal-shown' : ''} ${this.cssClasses}" role="dialog" aria-labelledby="title-${this.id}"
                aria-hidden="${!this.isOpen}" @click="${this.backdropClickHandler}">
                <div class="modal__dialog" role="document">
                    <section class="modal__content">
                        <header class="modal__header">
                            ${this.renderTitle}
                            <button @click="${this.close}" type="button" class="modal__btn-close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true"></span>
                            </button>
                        </header>
                        <div class="modal__body">
                            <slot name="${CONTENT_SLOT_NAME}"></slot>
                        </div>
                        <footer class="modal__footer ${this.displayFooter ? '' : 'is-hidden'}">
                            <slot name="${FOOTER_SLOT_NAME}"></slot>
                        </footer>
                    </section>
                </div>
            </div>
        `;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
