import { trackContentViews } from './gtm-initializer-content-views';
import { trackCallToActions } from './gtm-initializer-cta';
import { trackFooterLinks } from './gtm-initializer-footer-link';
import { trackHeaderEvents } from './gtm-initializer-header';
import { trackNavigationEvents } from './gtm-initializer-navigation';
import { trackProductSelection } from './gtm-initializer-product-selection';
import { trackForms } from './gtm-initializer-forms';
import { trackDamageClaims } from './gtm-initializer-damage-claims';
import { trackDownloads } from './gtm-download-event';
import { trackNavigationCta } from './gtm-initializer-navigation-cta';
import { trackContentCtaClicks } from './gtm-initializer-content-cta';
import { trackContentEngagementLinks } from './gtm-initializer-content-engagement';
import { trackCostCalculators } from './gtm-initializer-cost-calculator';

/**
 * Initializes Google Tag Manager tracking events
 * */
export function initializeGoogleTagManagerEvents(): void {
    trackContentViews();
    trackCallToActions();
    trackFooterLinks();
    trackNavigationEvents();
    trackHeaderEvents();
    trackProductSelection();
    trackForms();
    trackDamageClaims();
    trackDownloads();
    trackNavigationCta();
    trackContentCtaClicks();
    trackContentEngagementLinks();
    trackCostCalculators();
}
