import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { GoogleTagManagerService } from './google-tag-manager-service';

/**
 * Initialize navigation call to action SEO tracking
 *
 */
export function trackNavigationCta(): void {
    const navigationCtaElements = document.querySelectorAll<HTMLElement>('.js-gtm-navigation-cta');

    navigationCtaElements.forEach((element) => {
        element.addEventListener('click', (event: Event) => {
            const target = event.currentTarget as HTMLAnchorElement;
            const url = target.href && target.href.indexOf("#") != -1 ? '-' : target.href;
            let label =  target.getAttribute('data-url') || url;
            const action = target.innerText.trim() || target.getAttribute('aria-label');
            if (action && label) {
                GoogleTagManagerService.getInstance().pushEvent(GTMEvent.NavigationCtaClicks, GTMCategory.Navigation, `CTA clicks: ${action}`, label);
            }
        });
    });
}
