import { validationError, getErrorMessage } from './../validation-error';
import { mvcValidationDataAttributes } from './../mvc-validation-data-attribute';
import { ValidatableElement } from './../validatable-element';
import { isRequiredValid } from '../../validators/required/required-validator';
import { isCheckboxOrRadio } from '../../../../helpers/form-controls';

/**
 * Check if the passed field pass the required validation
 * @param el - the form field to validate
 * @param formElement - the form tag which will be useful if the control is part of a group like for radios
 * @param isWhitespaceInvalid - if true, a whitespace value will be considered as empty for the validation
 * @returns - an error message if there was an error or an empty string
 */
export function checkRequiredValidation(el: ValidatableElement, formElement: HTMLFormElement, isWhitespaceInvalid?: boolean): string {
    let control: ValidatableElement | RadioNodeList = el;
    const name = el.name;

    if (isCheckboxOrRadio(el)) {
        control = formElement.elements.namedItem(name) as RadioNodeList;
    }

    if (isRequiredValid(control.value, isWhitespaceInvalid)) {
        return '';
    }

    return getErrorMessage(control, mvcValidationDataAttributes.valRequired, validationError.isRequired);
}
