import { FormValidator } from './validations/form-validator/form-validator';
import { ValidatableElement } from './validations/validatable-element';
import { formCallbacks, FormCallbacks } from './callbacks/form-callbacks';
import { classNames } from '../../constants/class-names';
import { validateRecaptcha, toggleRecaptchaElements } from './recaptcha';
import { convertAspValidationTagsToHtml5 } from './validations/mvc-validation-converter';
import { makeHttpRequest } from './form-ajax';

/**
 * The class to identify a <form>
 */
const FORM_CLASS_SELECTOR = 'js-form';

/**
 * The attribute name for informing the form it has a Recaptcha
 */
const FORM_RECAPTCHA_ATTRIBUTE = 'data-form-recaptcha';

/**
 * The attribute name for informing the form it should display errors in a summary group
 */
const FORM_VALIDATION_SUMMARY_ATTRIBUTE = 'data-form-validation-summary';

/**
 * The list of input[type] which are not considered as validatable
 */
const nonValidatableInputTypes = ['submit', 'button', 'reset', 'hidden'];

/**
 * The types that are only checkable and which doesn't have same event like blur, etc
 */
const checkableInputTypes = ['radio', 'checkbox'];

/**
 * Class that decorate an existing form tag with custom behaviors
 *
 * How to :
 * - recaptcha
 *      => <form class="js-form" name="myFormName" data-form-recaptcha="nbOfTriesBeforeDisplayCapctha"> nbOfTriesBeforeDisplayCapctha, is optional and will be set to 0
 * - display validations in summary
 *      => <form class="js-form" name="myFormName" data-form-validation-summary> (default validation display is field-by-field)
 */
export class Form {
    public hasRecaptcha: boolean;
    public formName: string;
    public recaptchaNumberOfTries: number;
    public shouldDisplayValidationInSummary: boolean;

    /**
     * Inform whether the form should be submitted via ajax or trough standard http method
     */
    public isAjax = false;

    /**
     * Construct a Form instance around a given form tag
     * @param formElement - The <form> DOM element
     * @param formValidator - the instance which manage validations for fields
     */
    constructor(
        private formElement: HTMLFormElement,
        public formValidator: FormValidator,
    ) {
        this.formElement = formElement;
        this.formElement.noValidate = true; // we force disable the html5 validation to ensure that onsubmit event is triggered and that our custom validation is runned also
        this.formName = formElement.name ? 'VA-form-' + formElement.name : null;
        this.isAjax = !!this.formElement.dataset.isajax;
        this.hasRecaptcha = formElement.hasAttribute(FORM_RECAPTCHA_ATTRIBUTE);
        this.recaptchaNumberOfTries = this.hasRecaptcha ? parseInt(formElement.getAttribute(FORM_RECAPTCHA_ATTRIBUTE), 10) || 0 : 0;
        this.shouldDisplayValidationInSummary = formElement.hasAttribute(FORM_VALIDATION_SUMMARY_ATTRIBUTE);

        // init elements visibility
        toggleRecaptchaElements(this.formElement, this.isRecaptchaActive());

        // init events
        this.formElement.addEventListener('submit', this.onFormSubmit.bind(this));
        this.addEventsForValidation();
    }

    /**
     * Method to define when the Form should count the number of times the form has been submit (or just try to submit)
     * @returns True, if the Form should count the number of times the form has been submit
     */
    shouldCountSubmitTries(): boolean {
        // need a formName (name must be the same on page reload) to store the form in sessionstorage
        return this.hasRecaptcha &&
            this.formName !== null &&
            this.recaptchaNumberOfTries > 0;
    }

    /**
     * Method to define when the Form should display & check the Recaptcha is activated
     * @returns True, if the Recaptcha is activated for the Form
     */
    isRecaptchaActive(): boolean {
        const storedCounter = parseInt(sessionStorage.getItem(this.formName), 10) || 0;
        return this.hasRecaptcha &&
            this.formName !== null && // be sure to not check for null in the sessionStorage TODO this can be null if counter is set to 0
            storedCounter >= this.recaptchaNumberOfTries;
    }

    /**
     * Callback method triggered on form submit
     * @param event - The submit event
     */
    onFormSubmit(event: Event | CustomEvent): void {
        this.triggerFullFormValidation();

        // In case some browsers submit by default, with validation errors (like Safari & iOS Safari)
        if (!this.formElement.checkValidity()) {
            event.preventDefault();

            // scroll to 1st error
            const errorsElements = this.formElement.getElementsByClassName(classNames.hasError);
            if (errorsElements.length > 0) {
                errorsElements[0].scrollIntoView({ behavior: 'smooth' });
            }

            return;
        }

        if (this.isRecaptchaActive()) {
            const isRecaptchaValid = validateRecaptcha(event);
            if (!isRecaptchaValid) {
                event.preventDefault();
                toggleRecaptchaElements(this.formElement, true);
            }
        }

        if (this.shouldCountSubmitTries()) {
            let currentAttempt = parseInt(sessionStorage.getItem(this.formName), 10) || 0;
            currentAttempt++;
            sessionStorage.setItem(this.formName, currentAttempt.toString());
        }

        if (this.isAjax) {
            event.preventDefault();
            makeHttpRequest(this.formElement);
        }
    }

    /**
     * trigger a callback that has to be executed during the validation
     * @param element - is the element that is currently being validated
     */
    private triggerValidationCallback(element: ValidatableElement): void {
        const callbacksName = this.formElement.name;
        const validationCbName = callbacksName + 'Validation' as keyof FormCallbacks;

        const validate = formCallbacks[validationCbName];
        if (typeof validate === 'function') {
            validate(element);
        }
    }

    /**
     * Adds events related to HTML 5 Forms validations.
     * This includes form control events for live validation.
     */
    private addEventsForValidation(): void {
        const elementsToValidate = this.formElement.querySelectorAll<ValidatableElement>('input, textarea, select');

        elementsToValidate.forEach(element => {
            convertAspValidationTagsToHtml5(element);
            element.addEventListener('invalid', this.onFormControlInvalid.bind(this));

            // for checkable input (radio-checkbox) add eventListener on click
            if (element instanceof HTMLInputElement && checkableInputTypes.includes(element.type)) {
                element.addEventListener('click', this.onFieldInteraction.bind(this));
            }
            // for other input add event listener on blur & on input (triggered only when the input has already the error class)
            else {
                element.addEventListener('input', this.onFieldInteraction.bind(this));
                element.addEventListener('blur', this.onFieldInteraction.bind(this));
            }
        });

        // Disable browser native HTML5 validation error messages
        this.formElement.addEventListener('invalid', event => event.preventDefault(), true);
    }

    /**
     * This method run the validation on all fields of the form
     * This is useful to validate the whole form in 1 shot, like during the submit
     */
    private triggerFullFormValidation(): void {
        Array.from(this.formElement.elements).forEach(
            field => {
                if (this.isElementValidatable(field)) {
                    this.runValidationOnField(field as ValidatableElement);
                }
            }
        );
    }

    /**
     * Handler to be called when a form control is triggered as 'invalid'
     * @param event - the event triggered when the form control is invalid
     */
    private onFormControlInvalid(event: Event | CustomEvent): void {
        const element = event.target as ValidatableElement;
        this.removeElementValidationMessages(element);
        this.setFieldErrorState(element);
        this.triggerValidationCallback(element);
    }

    /**
     * Handler to be called after an interaction with the form control
     * It runs the validation and if invalid, triggers an 'invalid' event
     * @param event - the event triggered from the field
     */
    private onFieldInteraction(event: Event): void {
        const element = event.target as ValidatableElement;


        // for input event run validation only when there is an error => don't show an error when you type for the 1st time but will live correct the field when you try to fix it.
        if (event.type === 'input') {
            const formGroup = element.closest(`.${classNames.formGroup}`);
            if (formGroup?.classList.contains(classNames.hasError)) {
                this.runValidationOnField(element);
            }
        } else {
            this.runValidationOnField(element);
        }
    }

    /**
     * Run the validation
     * @param element - the field on which the validation has to be run
     */
    private runValidationOnField(element: ValidatableElement): void {
        this.removeFieldErrorState(element);
        const error = this.formValidator.runValidationForField(element, this.formElement);

        // we pass the potential error to the customValidity state. If empty, it will be considered as "no error"
        element.setCustomValidity(error);
        element.checkValidity();

        this.triggerValidationCallback(element);
    }

    /**
     * Get the id of a message zone (validation) for a given field
     * @param element - the element from which we want to get the id of the message zone
     * @returns - the id of the message
     */
    private getFieldMessageId(element: ValidatableElement) {
        return `error-for_${element.form.name}_${element.name}`;
    }

    /**
     * Set the error state on the field and add the message
     * @param element - the concerned form control
     */
    private setFieldErrorState(element: ValidatableElement): void {
        const formGroup = element.closest(`.${classNames.formGroup}`);

        if (formGroup) {
            formGroup.classList.add(classNames.hasError);
        }

        element.setAttribute('aria-invalid', 'true');
        element.setAttribute('aria-describedBy', this.getFieldMessageId(element));
        this.addElementValidationMessage(element);
    }

    /**
     * Inform if the passed element is considered as validatable in a form or not
     * @param element - the element to check if validatable or not
     * @returns - a boolean informing whether the element is validatable or not
     */
    private isElementValidatable(element: Element): element is ValidatableElement {
        // eslint-disable-next-line no-extra-parens -- reason: is needed for TS casting
        return element.tagName !== 'BUTTON' && !nonValidatableInputTypes.includes((element as HTMLInputElement).type);
    }

    /**
     * Remove the error state and message
     * @param element - the related form control
     */
    private removeFieldErrorState(element: ValidatableElement): void {
        const formGroup = element.closest(`.${classNames.formGroup}`);

        if (formGroup) {
            formGroup.classList.remove(classNames.hasError);
        }

        element.removeAttribute('aria-invalid');
        element.removeAttribute('aria-describedBy');
        this.removeElementValidationMessages(element);
    }

    /**
     * Method to insert validation messages in the DOM
     * @param element - the element that contains the validation message
     */
    private addElementValidationMessage(element: ValidatableElement): void {
        const message = element.validationMessage;
        const htmlMessage = this.createMessageHtml(element, message);
        if (this.shouldDisplayValidationInSummary) {
            this.createValidationSummary(htmlMessage);
        } else {
            this.createValidationMessageForField(element, htmlMessage);
        }
    }

    /**
     * Create the html validation message block to add in the document
     * @param element - the related field
     * @param message - the message to display
     * @returns the constructed message block as html string
     */
    private createMessageHtml(element: ValidatableElement, message: string): string {
        const htmlRootTag = this.shouldDisplayValidationInSummary ? 'li' : 'div';
        return `<${htmlRootTag} role="alert" class="${classNames.validationMessage} ${classNames.validationMessage}--error" id="${this.getFieldMessageId(element)}">${message}</${htmlRootTag}>`;
    }

    /**
     * Create the summary block at the end of the form which resume all validation errors
     * @param messageAsHtmlString - the html string of the message block to display
     */
    private createValidationSummary(messageAsHtmlString: string): void {
        this.formElement.insertAdjacentHTML('beforeend', messageAsHtmlString);
    }

    /**
     * Create the summary block at the end of the form which resume all validation errors
     * @param element - the related field
     * @param messageAsHtmlString - the html string of the message block to display
     */
    private createValidationMessageForField(element: ValidatableElement, messageAsHtmlString: string): void {
        if (checkableInputTypes.includes(element.type)) {
            // for radio and checkboxes, we don't put the same message at each element, but once for the group
            const parent = element.closest(`.${classNames.formGroup}`);
            if (parent) {
                parent.insertAdjacentHTML('beforeend', messageAsHtmlString);
            }
        } else {
            element.insertAdjacentHTML('afterend', messageAsHtmlString);
        }
    }

    /**
     * Method to remove from DOM the existing validation messages
     * @param element - the element that contains the validation message
     */
    private removeElementValidationMessages(element: ValidatableElement): void {
        const existingMsg = this.formElement.querySelector<HTMLElement>(`#${this.getFieldMessageId(element)}`);
        if (existingMsg) {
            existingMsg.remove();
        }
    }
}

/**
 * Method to initial js feature for all Forms on the current page
 * It will create a new Form (class) for each <form> present on the page
 */
export function initForms(): void {
    const validationHandler = FormValidator.getInstance();
    // eslint-disable-next-line no-undef -- because HTMLCollectionOf is wrongly considered as undefined
    const forms = document.getElementsByClassName(FORM_CLASS_SELECTOR) as HTMLCollectionOf<HTMLFormElement>;
    for (const form of forms) {
        new Form(form, validationHandler);
    }
}
