import Style from './va-alert.scss';
import { LitElement, html, property, customElement, css, unsafeCSS } from 'lit-element';
import { AlertType } from './alert-type';

/**
 * The tagname of the current component
 */
export const COMP_NAME = 'va-alert';

/**
 * An alert is an element which displays a notification bar
 */
@customElement(COMP_NAME)
export class VaAlert extends LitElement {
    /**
     * The alert mode
     */
    @property({ type: AlertType })
    type: AlertType = AlertType.Default;

    public get alertTypeClasses(): string {
        return this.type === AlertType.Default
            ? ''
            : `alert--${this.type.toString()}`;
    }

    /**
     * @override
     */
    render() {
        return html`
            <div class="alert ${this.alertTypeClasses}">
                <slot></slot>
            </div>
        `;
    }

    /**
     * Css styles
     * @returns the CSS styles
     */
    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
