import {GoogleTagManagerService} from './google-tag-manager-service';
import {GTMEvent} from "../../models/google-tag-manager/gtm-event";
import {GTMCategory} from "../../models/google-tag-manager/gtm-category";

const ignoreClass = '.js-gtm-navigation-cta';

/**
 * Tracks Footer Links Selection events
 */
export function trackFooterLinks(): void {
   document.querySelectorAll('.js-gtm-navigation-wrapper a').forEach(item => {
        if(item) {
            item.addEventListener('click', handleFooterClick)
        }
    });
}

/**
 * Target specific element and push event
 */
function handleFooterClick(evt: Event): void {
    const gtmService = GoogleTagManagerService.getInstance();
    const footerLinkTarget = evt.currentTarget;

    if (footerLinkTarget instanceof HTMLAnchorElement && !footerLinkTarget.matches(ignoreClass)) {
        const label = footerLinkTarget.getAttribute('aria-label') || footerLinkTarget.innerText.trim();
        if (footerLinkTarget.href && label) {
            gtmService.pushEvent(GTMEvent.FooterLinkClicks, GTMCategory.Navigation, `${GTMEvent.FooterLinkClicks}: ${label}`, footerLinkTarget.href);
        }
    }
}
