import Style from './va-nav-dropdown.scss';
import { LitElement, html, property, customElement, css, unsafeCSS } from 'lit-element';

/**
 * The name of the component's custom element
 */
export const COMP_NAME = 'va-nav-dropdown';

/**
 * The properties of a link in the dropdown
 */
export interface NavDropdownItem {
    label: string;
    url: string;
    isSelected?: boolean;
}

/**
 * A custom dropdown element used for the sub navigation on mobile
 */
@customElement(COMP_NAME)
export class VaNavDropdown extends LitElement {

    private _navList: Array<NavDropdownItem>;

    /**
     * The list of links to display in the nav dropdown
     * @value In the HTML attribute, it has to be a JSON string
     * that can be parsed by the JSON.parse method
     */
    @property({ type: Array })
    public set navList(val: Array<NavDropdownItem>) {
        if (!Array.isArray(val)) {
            val = [];
        }
        this._navList = val;
        const selectedItem = val.find(item => item.isSelected);
        this.selectedItemLabel = selectedItem ? selectedItem.label : '';
    }
    public get navList(): Array<NavDropdownItem> {
        return this._navList;
    }

    /**
     * The nav item in "navList" that is selected
     */
    public selectedItemLabel: string;

    /**
     * This value define the status of the dropdown menu.
     * If true, the menu is visible, else it's hidden
     */
    @property({ type: Boolean })
    public isActive = false;

    /**
     * This method toggle or force the visibility state of the dropdown menu
     * @param evt - the event that triggered the method if any
     * @param isActiveState - if defined, it forces the state instead of toggling
     * it. If true it will activate the menu, else it will hide it
     */
    toggleMenu(evt?: Event, isActiveState?: boolean): void {
        if (evt) {
            evt.preventDefault();
        }

        if (typeof isActiveState === 'undefined') {
            isActiveState = !this.isActive;
        }

        this.isActive = isActiveState;
    }

    /**
     * This method prevent reloading the page if the clicked link is the current page
     * @param evt - the click event
     * @param isLinkCurrentPage - if the current link clicked refers to the current page
     */
    linkClicked(evt: Event, isLinkCurrentPage?: boolean): void {
        if (isLinkCurrentPage) {
            evt.preventDefault();
            this.toggleMenu(null, false);
        }
    }

    /**
     * @override
     */
    render() {
        return html`<div class="va-nav-dropdown ${this.isActive ? 'is-active' : ''}">
            <button class="va-nav-dropdown__trigger"
                @click="${this.toggleMenu.bind(this)}"
                type="button"
                aria-haspopup="true"
                aria-expanded="${this.isActive.toString()}"
                >
                <span>${this.selectedItemLabel}</span>
                <span class="va-nav-dropdown__caret"></span>
            </button>
            <ul class="va-nav-dropdown__menu">
                ${this.navList.map(item => html`
                    <li>
                        <a href="${item.url}"
                            @click="${(evt: Event) => this.linkClicked(evt, item.isSelected)}"
                            class="va-nav-dropdown__link ${item.isSelected ? 'is-selected' : ''}">
                            ${item.isSelected ?
                                html`<span class="va-nav-dropdown__check"></span>` :
                                ''}
                            <span class="va-nav-dropdown__label">${item.label}</span>
                        </a>
                    </li>
                `)}
            </ul>
        </div>`;
    }

    /**
     * Styles relative to the component
     * @returns the CSS styles
     */
    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
