

/**
 * This object contains all data attribute passed to a field by asp validation.
 * This attribute name has to be fetched trhough `field.dataset` and not via the
 * `getAttribute` function or you wouldn't find anything.
 */
export const mvcValidationDataAttributes = {
    /**
     * This data attribute is present if there's any validation on that element
     * from the asp side.
     */
    val: 'data-val',

    /**
     * This data attribute is present when a field is required
     * Its value is the error message to display if invalid
     */
    valRequired: 'data-val-required',

    /**
     * This data attribute is present when a field has a maximum number of characters
     * Its value is the number of character max
     */
    valMaxLength: 'data-val-max-length',

    /**
     * This data attribute is present when a field has to be a date
     * Its value is the error message to display if invalid
     */
    valDate: 'data-val-date',

    /**
     * This data attribute is present when a field has to be an email
     * Its value is the error message to display if invalid
     */
    valEmail: 'data-val-email',

    /**
     * This data attribute is present for a date in specific range
     * Its value is the error message to display if invalid and already contains the min/max
     */
    valDatetimerangevalidation: 'data-val-datetimerangevalidation',

    /**
     * This data attribute is present when a date has to be given in a specific format
     * It's value is the format of the date (ex: "dd.MM.yyyy")
     */
    valDatetimerangevalidationDateformat: 'data-val-datetimerangevalidation-dateformat',

    /**
     * This data attribute is present to limit the date to a max value
     * It's value is the max date (ex: "16.03.2021" according to the date format data attribute)
     */
    valDatetimerangevalidationMaxdatetime: 'data-val-datetimerangevalidation-maxdatetime',

    /**
     * This data attribute is present to limit the date to a min value
     * It's value is the min date (ex: "01.01.1920" according to the date format data attribute)
     */
    valDatetimerangevalidationMindatetime: 'data-val-datetimerangevalidation-mindatetime',
};
