import {GoogleTagManagerService} from './google-tag-manager-service';
import {GTMEvent} from "../../models/google-tag-manager/gtm-event";
import {GTMCategory} from "../../models/google-tag-manager/gtm-category";

/**
 * Tracks Content CTA events
 */
export function trackContentCtaClicks(): void {
   document.querySelectorAll<HTMLAnchorElement>('.js-gtm-content-cta-wrapper a').forEach(item => {
        item.addEventListener('click', handleContentClick)
    });
}

/**
 * Target specific element and push event
 */
function handleContentClick(evt: Event): void {
    const contentLinkTarget = evt.currentTarget;

    if (contentLinkTarget instanceof HTMLAnchorElement) {
        const label = contentLinkTarget.getAttribute('aria-label') || contentLinkTarget.innerText.trim();
        const url = contentLinkTarget.href && contentLinkTarget.href.indexOf("#") != -1 ? '-' : contentLinkTarget.href;

        if (url  && label) {
            GoogleTagManagerService.getInstance().pushEvent(GTMEvent.ContentCtaClicks, GTMCategory.ContentEngagement, `CTA clicks: ${label}`, url);
        }
    }
}