import { GoogleTagManagerService } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';

/**
 * Css class name to filter product selection events
 */
const productSelectionEventFilter = '.js-gtm-product-selection';

/**
 * Product selection GTM event action
 */
export const productSelectionEventAction = 'Select product';

/**
 * Tracks Product Selection events
 *
 * @param {Event} event The event
 */
function trackProductSelectionHandler(event: Event) : void {
    const targetElement = event.currentTarget as HTMLElement;
    const gtmLabel = targetElement.dataset.gtmLabel;

    if (gtmLabel) {
        GoogleTagManagerService.getInstance().pushEvent(GTMEvent.ProductSelection, GTMCategory.Navigation, productSelectionEventAction, gtmLabel);
    }
}

/**
 * Initializes the tracking product selection
 */
export function trackProductSelection() : void {
    document.querySelectorAll(productSelectionEventFilter).forEach((elem) => elem.addEventListener('click', trackProductSelectionHandler, false));
}
