import {css} from 'lit-element'; export default css`:host{display:block}:host(:last-child){border-bottom:1px solid #f6f5f1}.accordion-item{border-top:1px solid #f6f5f1}.accordion-item__trigger{display:flex;align-items:center;width:100%;padding:1.5rem 0;border:0;background-color:transparent;color:#585858;font-size:35.2px;font-size:2.2rem;text-align:left;text-decoration:none}.accordion-item__trigger:active,.accordion-item__trigger:focus,.accordion-item__trigger:hover{outline:0;color:#009640}.accordion-item__trigger:focus{box-shadow:0 0 0 .25rem rgba(0,150,64,.35)}.accordion-item__title{margin:0;font-family:the_sans_plain;font-size:35.2px;font-size:2.2rem;font-weight:400;line-height:1.1}.accordion-item__content{max-height:0;transition:max-height .3s ease-out;overflow:hidden}.accordion-item__icon{display:inline-block;position:relative;width:15px;min-width:15px;height:15px;margin-right:1.5rem}.accordion-item__icon:after,.accordion-item__icon:before{display:inline-block;position:absolute;top:0;left:0;width:100%;height:1px;transition:transform .3s ease-out;background-color:#009640;content:""}.accordion-item__icon:before{margin-top:6px;transform:rotate(90deg);background-color:#009640}.is-active .accordion-item__icon:before{transform:none}.accordion-item__icon:after{transform:translateY(6px) rotate(0)}.is-active .accordion-item__icon:after{transform:translateY(6px) rotate(180deg)}`;
    