import { SwipeEventDetail, SwipedEventNames } from './swipe-event-detail';

/**
 * Class to detect and generate custom swipe events. 
 * Registers touch event handlers to the document to perform the required logic to identify the desired events.
 */
export class SwipeEventsGenerator {
    private static instance : SwipeEventsGenerator;

    private startSwipeElement : EventTarget;
    private startSwipeX : number;
    private startSwipeY : number;
    private swipedDistanceX : number;
    private swipedDistanceY : number;

    /**
     * Touch events distance treshold. Used to distinct swipe from tap/double tap events.
     * Threshold is configured to be 1% of page width.
     */
    private swipeTreshold: number;

    private constructor() {
        document.addEventListener('touchstart', this.touchStartHandler, false);
        document.addEventListener('touchmove', this.touchMoveHandler, false);
        document.addEventListener('touchend', this.touchEndHandler, false);
    }

    /**
     * Store the touch element and initial position.
     * @param event touch start event
     */
    private touchStartHandler(event: TouchEvent) : void {
        let  pageWidth = window.innerWidth || document.body.clientWidth;

        this.startSwipeElement = event.target;
        this.startSwipeX = event.touches[0].clientX;
        this.startSwipeY = event.touches[0].clientY;
        this.swipedDistanceX = 0;
        this.swipedDistanceY = 0;
        this.swipeTreshold = Math.max(1, Math.floor(0.01 * (pageWidth)));
    }

    /**
     * Calculate the swiped distance since the stored touchstart event and the moved one.
     * @param event touch move event
     */
    private touchMoveHandler(event: TouchEvent) : void {
        if(!this.startSwipeX || !this.startSwipeY){
            return;
        }

        this.swipedDistanceX = this.startSwipeX - event.touches[0].clientX;
        this.swipedDistanceY = this.startSwipeY - event.touches[0].clientY;
    }
    
    /**
     * Calculates swiped direction and triggers custom event.
     * If touch ends on another target element this process is canceled.
     * If swiped distance is lower than swipe threshold, the event will be considered a tap instead of swipe.
     * @param event touch end event
     */
    private touchEndHandler(event: TouchEvent) : void {
        if(this.startSwipeElement !== event.target){
            return;
        }

        if (Math.abs(this.swipedDistanceX) > this.swipeTreshold || Math.abs(this.swipedDistanceY) > this.swipeTreshold) {
            var swipeEventType = '';

            if (Math.abs(this.swipedDistanceX) > Math.abs(this.swipedDistanceY) ) {
                swipeEventType = this.swipedDistanceX > 0 ? SwipedEventNames.left : SwipedEventNames.right;
            } else {
                swipeEventType = this.swipedDistanceY > 0 ? SwipedEventNames.up : SwipedEventNames.down;
            }
    
            let eventDetails = new SwipeEventDetail();
            
            eventDetails.direction = swipeEventType.replace(/swiped-/, '');
            eventDetails.swipedElement = this.startSwipeElement;

            this.startSwipeElement.dispatchEvent(new CustomEvent<SwipeEventDetail>(swipeEventType, { 
                bubbles: true, 
                cancelable: true, 
                composed: true,
                detail: eventDetails
            }));
        }

        this.startSwipeX = null;
        this.startSwipeY = null;
        this.swipedDistanceX = null;
        this.swipedDistanceY = null;
        this.startSwipeElement = null;
        this.swipeTreshold = null;
    }

    /**
     * Initializes the swipe events generator.
     */
    static initializeGenerator(): void {
        if(!SwipeEventsGenerator.instance){
            SwipeEventsGenerator.instance = new SwipeEventsGenerator();
        }
    }
}