import Style from './va-accordion-item.scss';
import { LitElement, html, property, customElement, css, unsafeCSS, TemplateResult } from 'lit-element';
import { ACCORDION_CUSTOM_EVENT_NAMES } from './custom-event-names';

/**
 * The tagname of the current component
 */
export const COMP_NAME = 'va-accordion-item';

const accordionItemIdPrefix = 'va-accordion-';
const accordionItemContentClassName = 'accordion-item__content';

/**
 * An accordion item element on which we can collapse/expand the content
 * This element can be stacked multiple times to create an accordion
 */
@customElement(COMP_NAME)
export class VaAccordionItem extends LitElement {
    /**
     * A counter used to identify the current accordion instance
     */
    static _idCounter = 0;

    /**
     * The title that will be placed in the accordion header
     */
    @property({ type: String })
    public title = '';

    /**
     * The property informing if the accordion is currently expanded
     */
    @property({ type: Boolean })
    public isExpanded = false;

    /**
     * Html tag name for the title of the accordion.
     * Accepted values: ['h1', 'h3', 'span']
     * Default value is 'h1'.
     * Any invalid value will fallback to default.
     */
    @property({ type: String })
    public titleHtmlTagName = 'h1';

    /**
     * Get the id of the content part in the template
     * @returns the id
     */
    get contentId(): string {
        return `${this.id}-content`;
    }

    /**
     * Construct a new instance of the accordion item and define a unique id
     */
    constructor() {
        super();
        this.id = accordionItemIdPrefix + VaAccordionItem._idCounter++;
    }

    /**
     * Component Lifecycle method called after the elements DOM has been updated the first time
     * @see https://lit-element.polymer-project.org/guide/lifecycle#firstupdated
     */
    async firstUpdated() {
        if (this.isExpanded) {
            // IE fix: height is only set after "firstUpdate" has been executed
            await new Promise((r) => window.setTimeout(r, 0));
            this._resetMaxHeight();
        }
    }

    /**
     * Toggle the collapsed state of the accordion
     * @param evt - the event passed when the action is triggered
     */
    toggleExpand(evt?: Event): void {
        if (evt && evt.preventDefault) {
            evt.preventDefault();
        }

        if (evt?.stopPropagation) {
            evt.stopPropagation();
        }

        this.isExpanded = !this.isExpanded;
        this._resetMaxHeight();

        this.dispatchEvent(new CustomEvent(ACCORDION_CUSTOM_EVENT_NAMES.ItemClick, {
            bubbles: true,
            cancelable: true,
            composed: true,
            detail: { title: this.title, isExpanded: this.isExpanded }
        }));
    }

    /**
     * This function will check if the maxHeight is set to the content,
     * in order to guarantee that the transition effect will be applied correctly
     */
    private _resetMaxHeight(): void {
        const contentClassSelector = '.' + accordionItemContentClassName;
        const panel = this.shadowRoot.querySelector<HTMLDivElement>(contentClassSelector);

        if (panel && panel.style) {
            panel.style.maxHeight = panel.style.maxHeight || panel.scrollHeight === 0 ? null : panel.scrollHeight + 'px';
        }
    }

    /**
     * Renders title based on the configuration "titleHtmlTagName".
     * We are using switch statement with static element definition because
     * parsing of html templates on Lit-Element does not allow to dynamically specify
     * html tag names through expessions like <${this.titleHtmlTagName}></${this.titleHtmlTagName}>
     * @returns the title template result
     */
    renderTitle(): TemplateResult {
        switch (this.titleHtmlTagName) {
        case 'span':
            return html`<span class="accordion-item__title" role="heading" aria-level="1">
                                ${this.renderButton()}
                            </span>`;
        case 'h3':
            return html`<h3 class="accordion-item__title">
                                ${this.renderButton()}
                            </h3>`;

        default:
            return html`<h1 class="accordion-item__title">
                                ${this.renderButton()}
                            </h1>`;
        }
    }

    renderButton(): TemplateResult {
        return html`<button class="accordion-item__trigger" @click="${this.toggleExpand}" aria-controls="${this.contentId}"
                            aria-expanded="${this.isExpanded}" type="button">
                            <i class="accordion-item__icon"></i>
                            <span>${this.title}</span>
                        </button>`;
    }

    /**
     * @override
     */
    render() {
        return html`
            <section class="accordion-item ${this.isExpanded ? 'is-active' : ''}" role="region">
                <header class="accordion-item__header">
                    ${this.renderTitle()}
                </header>
                <div id="${this.contentId}" class="${accordionItemContentClassName}">
                    <slot></slot>
                </div>
            </section>`;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
