import { validationError, getErrorMessage } from '../validation-error';
import { mvcValidationDataAttributes } from './../mvc-validation-data-attribute';
import { ValidatableElement } from './../validatable-element';
import { isDateValidator, isDateInRangeValidator } from '../../validators/date/date-validator';

/**
 * Access the field and get the date format attribute
 * @param _el - the html element on which we get the format attribute
 * @returns the date format
 */
function getDateFormat(_el: ValidatableElement): string {
    return _el.getAttribute(mvcValidationDataAttributes.valDatetimerangevalidationDateformat);
}

/**
 * Check if the passed field pass the required validation
 * @param _el - the form field to validate
 * @returns an error message if there was an error or an empty string
 */
export function checkDateIsInRange(_el: ValidatableElement): string {
    const currentValue = _el.value;
    const dateFormat = getDateFormat(_el);
    const maxDateString = _el.getAttribute(mvcValidationDataAttributes.valDatetimerangevalidationMaxdatetime);
    const minDateString = _el.getAttribute(mvcValidationDataAttributes.valDatetimerangevalidationMindatetime);
    const errorMessage = getErrorMessage(_el, mvcValidationDataAttributes.valDatetimerangevalidation, validationError.dateNotInRange);

    if (!currentValue || !maxDateString || !minDateString) {
        return '';
    }

    if (isDateInRangeValidator(currentValue, minDateString, maxDateString, dateFormat)) {
        return '';
    }
    return errorMessage ? errorMessage : validationError.dateNotInRange;
}

/**
 * Check that the date in the field is a valid date or an empty string
 * @param _el - the form field to validate
 * @returns an error message if there was a validation error
 */
export function checkDateIsValid(_el: ValidatableElement): string {
    const currentValue = _el.value;

    // empty is ok as it has to be validated with "required"
    if (!currentValue) {
        return '';
    }

    const dateFormat = getDateFormat(_el);
    if (isDateValidator(currentValue, dateFormat)) {
        return '';
    }

    const errorMessage = getErrorMessage(_el, mvcValidationDataAttributes.valDate, validationError.dateIsInvalid);
    return errorMessage;
}
