import { GoogleTagManagerService } from './google-tag-manager-service';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';

/**
 * Target specific element and push event
 * @param evt Event
 */
function handleContentEngagementLinkClick(evt: Event): void {
    const contentLinkTarget = evt.currentTarget as HTMLAnchorElement;

    const label = contentLinkTarget.title;
    const url = contentLinkTarget.href;

    if (url && label) {
        GoogleTagManagerService.getInstance().pushEvent(GTMEvent.ContentViews, GTMCategory.ContentEngagement, `${GTMEvent.ContentViews}: ${label}`, url);
    }
}

/**
 * Tracks Content Engagement events
 */
export function trackContentEngagementLinks(): void {
    document.querySelectorAll<HTMLAnchorElement>('.js-gtm-content-engagement a').forEach(item => item.addEventListener('click', handleContentEngagementLinkClick));
}
