export type MoveInDomFn = (placeholderSelector: string, sourceSelector: string) => void;

/**
 * Method to allow moving a DOM element to another target element
 * If there is nothing to move (no source), there is no need for the placeholder to existing anymore.
 * @param placeholderSelector The selector for the target element in DOM
 * @param sourceSelector The selector for the element to move
 */
export const moveInDom: MoveInDomFn = function (placeholderSelector: string, sourceSelector: string) {
    const sourceElement = document.querySelector(sourceSelector);
    const placeholderElement = document.querySelector(placeholderSelector);

    if (placeholderElement) {
        if (sourceElement) {
            placeholderElement.appendChild(sourceElement);
        } else {
            placeholderElement.remove();
        }
    }
}
