const radioType = 'radio';
const checkboxType = 'checkbox';

/**
 * Check if the passed element is an HTMLInputElement
 * @param el - the element to check
 * @returns a boolean informing whether the element is an input tag or not
 */
export function isInputElement(el: Element): el is HTMLInputElement {
    return el instanceof HTMLInputElement;
}

/**
 * Check if the passed element is an HTMLInputElement with type radio or checkbox
 * @param el - the element to check
 * @returns a boolean informing whether the element is an input of type radio or checkbox
 */
export function isCheckboxOrRadio(el: Element): el is HTMLInputElement {
    return isInputElement(el) && [radioType, checkboxType].includes(el.type);
}
