import { GoogleTagManagerService } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { GtmApplicationStore } from '../../models/google-tag-manager/gtm-application-store';
import { getFileNameFromLinkUrl } from '../../helpers/files';

const pictureWidgetDownloadUrlRegex = new RegExp(/\/download\/([0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12})\/?/i);
const appleDownloadLinks = `a[href*='appstore.com'], a[href*='itunes.apple.com']`;
const androidDownloadLinks = `a[href*='play.google.com']`;

/**
 * Pushes the GTM app download event
 * @param applicationStoreType gtm application store type
 */
function pushAppDownloadEvent(applicationStoreType: GtmApplicationStore) : void {
    GoogleTagManagerService.getInstance().pushEvent(GTMEvent.AppDownload, GTMCategory.ContentEngagement, 'App download clicks', applicationStoreType);
}

/**
 * Retrieves the picture widget description for the gtm download event
 * @param downloadLink Picture widget download link element
 * @returns link title if available, otherwise description element text.
 */
function getPictureWidgetEventDescription(downloadLink: HTMLAnchorElement) : string {
    return downloadLink.title || downloadLink.closest('.block-content-txt')?.querySelector<HTMLElement>('.description')?.innerText;
}

/**
 * Pushes the gtm download event
 * @param target clicked anchor element
 */
function pushGTMDownloadEvent(target: HTMLAnchorElement) : void {
    const url = target.getAttribute('href');
    let docFileName = getFileNameFromLinkUrl(target);

    // Is it a standard sitefinity file download
    if (docFileName) {
        GoogleTagManagerService.getInstance().pushEvent(GTMEvent.DocumentDownload, GTMCategory.ContentEngagement, `Document downloads: ${docFileName}`, url);
        // Tracking downloads events from the custom widget 'Photo'
    } else if (target.matches('a.btn-arrow') && url && url.match(pictureWidgetDownloadUrlRegex)) {
        GoogleTagManagerService.getInstance().pushEvent(GTMEvent.DocumentDownload, GTMCategory.ContentEngagement, `Document downloads: ${getPictureWidgetEventDescription(target)}`, url);
        // Is an apple app download
    } else if (target.matches(appleDownloadLinks)) {
        pushAppDownloadEvent(GtmApplicationStore.Apple);
        // Is an android app download
    } else if (target.matches(androidDownloadLinks)) {
        pushAppDownloadEvent(GtmApplicationStore.Android);
    }
}

/**
 * Track download event handler.
 * Download event is processed if target is an anchor element and isn't set to be ignored by gtmignore attribute.
 * @param event click event instance
 */
function trackDownloadEventHandler(event: Event) : void {
    if (event.target instanceof HTMLAnchorElement && event.target.dataset.gtmIgnore !== true.toString()) {
        pushGTMDownloadEvent(event.target as HTMLAnchorElement);
        event.stopPropagation();
    }
}

/**
 * Tracks gtm download events
 */
export function trackDownloads() : void {
    document.addEventListener('click', trackDownloadEventHandler, false);
}
