/**
 *
 * Function that regroups small functions link to various components
 * But the js logic of the component is so small that it don't need a specific file
 * @export
 */
export function initMisc(): void {
   initRatioShadow();
}

/**
 * Apply the shadow class on a ratio (img/video/...)
 * Once the media is loaded.
 * This prevent to display the shadow on a white bg
 *
 * @returns {void}
 */
function initRatioShadow(): void {

    const RATIO_SELECTOR = '.js-ratio-shadow';
    const RATIO_ITEM_SELECTOR = '.ratio__item';
    const RATIO_SHADOW_CLASS = 'ratio--shadow';
    const ratios = document.querySelectorAll(RATIO_SELECTOR);

    ratios.forEach(ratio => {
        const ratioItem: HTMLElement = ratio.querySelector(RATIO_ITEM_SELECTOR);

        // once the ratio media is loaded we apply the shadow call on the ratio base element
        ratioItem?.addEventListener("load", (_event) => {
            ratio.classList.add(RATIO_SHADOW_CLASS);
        })

    });
}
