import { GoogleTagManagerService } from './google-tag-manager-service';

const ctaSelector = [
    '.cta-wrapper a.btn-arrow-secondary',
    '.cta-wrapper a.btn-arrow',
    '.cta-wrapper-lg a.btn-arrow-secondary',
    '.block-content-personal-area .cta a.btn-arrow',
    '.js-gtm-content-cta'
].join(',');

const clickHandler = (event: Event) => {
    if (event.target instanceof HTMLAnchorElement) {
        const anchor = event.target as HTMLAnchorElement;

        if (anchor.matches(ctaSelector)) {
            GoogleTagManagerService.getInstance().pushContentCta(anchor.innerText, anchor.href);
        }
    }
}

/**
 * Tracks Call to Actions (CTA) events
 */
export function trackCallToActions(): void {
    document.addEventListener('click', clickHandler, false);
}
