import { SLIDER_CUSTOM_EVENT_NAMES } from '../../../../webcomponents/va-slider/custom-event-names';
import { getFormattedCurrency } from '../../helpers/formatters';

export const CALCULATOR_INTO_DATA_ATTRIBUTE = 'data-calculator-map-into';
export const CALCULATOR_FROM_DATA_ATTRIBUTE = 'data-calculator-map-from';

/**
 * Function to hook inputs, va-slider and spans, given the context of calculators
 * As an example, when you move the slider, update the text-box and "spans" with value placeholders. and vice-versa
 */
export function initCalculatorHooks(): void {
    const inputsToHook = document.querySelectorAll(`input[${CALCULATOR_INTO_DATA_ATTRIBUTE}]`);
    const placeholdersToHook = document.querySelectorAll(`[${CALCULATOR_FROM_DATA_ATTRIBUTE}]`);

    inputsToHook.forEach((input: HTMLInputElement) => {
        input.addEventListener('input', _ => {
            const targetInputIds = input.dataset.calculatorMapInto.split(',');
            targetInputIds.forEach(targetInputId => {
                const targetElement: HTMLElement = document.getElementById(targetInputId.trim());
                if (!targetElement) {
                    return;
                }

                if (!isNaN(Number(input.value))) {
                    adjustInputValueWithMinMaxBoundaries(targetElement, input);

                    targetElement.setAttribute('value', input.value);
                    targetElement.innerHTML = input.value;
                }
            });
        });
    });

    placeholdersToHook.forEach((placeholder: HTMLInputElement | HTMLSpanElement) => {
        const sourceInputIds = placeholder.dataset.calculatorMapFrom.split(',');
        sourceInputIds.forEach(sourceInputId => {
            const sourceInputElement = document.getElementById(sourceInputId.trim());
            if (!sourceInputElement) {
                return;
            }

            sourceInputElement.addEventListener('input', _ => {
                setPlaceholder(placeholder, (sourceInputElement as HTMLInputElement).value);
            });
            sourceInputElement.addEventListener(SLIDER_CUSTOM_EVENT_NAMES.SliderValueChanged, (event: CustomEvent) => {
                setPlaceholder(placeholder, event.detail.value.toString());
            });
        });
    });
}

function adjustInputValueWithMinMaxBoundaries(targetElement: HTMLElement, input: HTMLInputElement) {
    if (targetElement.hasAttribute('max') || targetElement.hasAttribute('min')) {
        const min = targetElement.getAttribute('min');
        const max = targetElement.getAttribute('max');

        if (Number(max) < Number(input.value)) {
            input.value = max;
        } else if (Number(min) > Number(input.value)) {
            input.value = min;
        }
    }
}

function setPlaceholder(placeholder: HTMLInputElement | HTMLSpanElement | HTMLAnchorElement, value: string) {
    if (placeholder instanceof HTMLInputElement) {
        placeholder.value = value;
    } else if (placeholder instanceof HTMLSpanElement) {
        placeholder.innerText = getFormattedCurrency(Number(value));
    } else if (placeholder instanceof HTMLAnchorElement) {
        if (!isNaN(+value)) {
            placeholder.href = `${placeholder.dataset.originalHref}?value=${value}`;
        }
    }

    // If the placeholder is an input, make sure we trigger the corresponding events to a value change
    // 'input' will guarantee all placeholders hooks to be reactive
    // 'blur' will guarantee all placeholders post-format to be applied
    if (placeholder instanceof HTMLInputElement) {
        placeholder.dispatchEvent(new Event('input'));
        placeholder.dispatchEvent(new Event('blur'));
    }
}
