import { GoogleTagManagerService } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { getFileNameFromLinkUrl } from '../../helpers/files'

/**
* Tracks Damage Claims events
*/
export function trackDamageClaims(): void {
    document.addEventListener('click', (e) => {
        const target = e.target as HTMLElement;

        if (target instanceof HTMLAnchorElement && target.matches('.js-gtm-damage-claims') && target.dataset['gtmIgnore'] !== true.toString()) {
            const fileName = getFileNameFromLinkUrl(target);
            const label = target.dataset['gtmLabel'];
           
            GoogleTagManagerService.getInstance().pushEvent(
                GTMEvent.DamageClaims,
                GTMCategory.ClientEnquiries,
                `Damage claim: ${target.dataset['gtmAction']}`,
                fileName && fileName !== label ? `${fileName}; ${label}` : label);
        }
    }, false);
}
