import { ExtendedWindow } from './models/extended-window.model';
import { initRecaptcha, validateRecaptcha } from './components/form/recaptcha';
import { moveInDom } from './helpers/dom';

declare const window: ExtendedWindow;

/**
 * Init global variables required by the application
 */
export function initGlobals() {
    window.initializeGoogleRecaptcha = initRecaptcha;
    window.validateRecaptcha = validateRecaptcha;
    window.moveInDom = moveInDom;
}
