import { lifeCalculatorSuccess, lifeCalculatorFinally, lifeCalculatorValidation } from './life-calculator';
import { PromiseCallback } from '../../../types';


/**
 * This object describe the available callbacks declared for the forms.
 * All callbacks names have to follow the following pattern: '<categoryName>[Success|Error|Finally]'
 */
export interface FormCallbacks {
    /**
     * The success callback for the ajax promise of the life calculator form widget, if any
     */
    lifeCalculatorSuccess?: PromiseCallback;

    /**
     * The rejection callback for the ajax promise of the life calculator form widget, if any
     */
    lifeCalculatorError?: PromiseCallback;

    /**
     * The finally callback for the ajax promise of the life calculator form widget, if any
     */
    lifeCalculatorFinally?: PromiseCallback;

    /**
     * The validation callback for the life calculator form widget, if any
     */
    lifeCalculatorValidation?: PromiseCallback;
}

/**
 * This object contains the available callbacks declared for the forms
 */
export const formCallbacks: FormCallbacks = {
    lifeCalculatorSuccess,
    lifeCalculatorFinally,
    lifeCalculatorValidation,
};
