import Style from './va-stepper.scss';
import { LitElement, html, property, customElement, TemplateResult, css, unsafeCSS } from 'lit-element';

/**
 * This interface is the signature of the navigation description to pass to the component
 */
export interface NavigationDetail {
    link: string;
    label: string;
}

/**
 * The custom-element name
 */
export const COMPONENT_NAME = 'va-stepper';

/**
 * The class used on the active step link
 */
export const activeItemStepClass = `${COMPONENT_NAME}__link--is-active`;

/**
 * The class used on the step link which is complete
 */
export const completeItemStepClass = `${COMPONENT_NAME}__link--is-complete`;

/**
 * A stepper component which is used to create a multi step navigation
 */
@customElement(COMPONENT_NAME)
export class VaStepper extends LitElement {
    /**
     * Define the navigation items of the stepper
     */
    @property({ type: Object, attribute: 'nav-items' })
    navItems: Array<NavigationDetail>;

    /**
     * Define which step is the currently selected one
     */
    @property({ type: Number, attribute: 'current-step' })
    currentStep = 1;

    /**
     * Check if the passed step is active
     * @param stepIdx - the index of the step
     * @returns a boolean informing whether the passed step is the active one
     */
    isStepActive(stepIdx: number): boolean {
        return this.currentStep === stepIdx;
    }

    /**
     * Check if the passed step is completed, which means that the current step is bigger than the passed one
     * @param stepIdx - the index of the step
     * @returns a boolean informing whether the passed step is a completed one
     */
    isStepCompleted(stepIdx: number): boolean {
        return stepIdx < this.currentStep;
    }

    /**
     * Define what symbole to display. We display a checkmark for completed steps and the number of the step for others
     * @param stepIdx - the index of the step
     * @returns the character to display in the step zone
     */
    getStepSymbol(stepIdx: number): string {
        return this.isStepCompleted(stepIdx) ? '' : (stepIdx + 1).toString();
    }

    /**
     * Method triggered when we click on a element
     * @param event - the click event
     * @param idx - the idx of the nav item clicked
     * @param item - the nav item object
     */
    onClickStep(event: Event, idx: number, item: NavigationDetail) {
        if (item.link === '#') {
            event.preventDefault();
        }
        this.changeNavTo(idx);
    }

    /**
     * Change the current active nav item to the one related to the passed param
     * @param newItemIdx - the nav index to go to
     */
    changeNavTo(newItemIdx: number): void {
        const isIdxValid = newItemIdx >= 0 && newItemIdx < this.navItems.length;
        if (!isIdxValid || this.currentStep === newItemIdx) {
            return;
        }

        const previousStepIdx = this.currentStep;
        this.currentStep = newItemIdx;
        const detail = {
            previousStepIdx,
            newStepIdx: this.currentStep,
        };
        this.dispatchEvent(new CustomEvent('change', { detail }));
    }

    /**
     * Go to the next available step if any
     */
    goToNextStep(): void {
        const nextStep = this.currentStep + 1;
        this.changeNavTo(nextStep);
    }

    /**
     * Go to the previous step if any
     */
    goToPreviousStep(): void {
        const previousStep = this.currentStep - 1;
        this.changeNavTo(previousStep);
    }

    /**
     * Create the html of a single navigation item
     * @param item - the item to render
     * @param idx - the index of that item in the list
     * @returns the html of that navigation item
     */
    renderItem(item: NavigationDetail, idx: number): TemplateResult {
        if (!item.link) {
            item.link = '#';
        }
        const isStepActive = this.isStepActive(idx);
        const isActiveClass = isStepActive ? activeItemStepClass : '';
        const isCompleteClass = this.isStepCompleted(idx) ? completeItemStepClass : '';
        const ariaSelected = isStepActive ? 'true' : 'false';

        return html`
            <li class="${COMPONENT_NAME}__step">
                <a href="${item.link}" @click="${(evt: Event) => this.onClickStep(evt, idx, item)}" data-idx="${idx}"
                    class="${COMPONENT_NAME}__link ${isActiveClass} ${isCompleteClass}" aria-selected="${ariaSelected}">
                    <span class="${COMPONENT_NAME}__symbol">${this.getStepSymbol(idx)}</span>
                    <span class="${COMPONENT_NAME}__label">${item.label}</span>
                </a>
            </li>
        `;
    }

    /**
     * Render the webcomponent's html
     * @returns the html to render
     */
    render(): TemplateResult {
        return html`
            <nav class="${COMPONENT_NAME}" role="navigation">
                <ol class="${COMPONENT_NAME}__step-list">${this.navItems.map(this.renderItem.bind(this))}</ol>
            </nav>
        `;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
