/**
 * Validate a required value to ensure it's valid
 * @param value - the value to validate
 * @param isWhitespaceInvalid - if true, one or more whitespace value only will be considered as invalid
 * @returns a boolean informing whether the value is valid or not
 */
export function isRequiredValid(value: string, isWhitespaceInvalid?: boolean): boolean {
    if (isWhitespaceInvalid) {
        value = value.trim();
    }

    return value !== '';
}
