import { AnimationSettings } from "./animation-settings";

/**
 * Cross fade animation settings.
 */
export const crossfadeAnimationSettings: AnimationSettings = {
    showKeyFrames: [
        { opacity: 0 },
        { opacity: 1 }
    ],
    hideKeyFrames: [
        { opacity: 1 },
        { opacity: 0 }
    ],
    options: {
        duration: 1000,
        easing: 'ease-in-out'
    }
};

/**
 * Slide right-left animation settings.
 */
export const slideAnimationSettings: AnimationSettings = {
    showKeyFrames: [
        { opacity: 0, transform: 'translateX(100%)' },
        { opacity: 1, transform: 'translateX(0%)'},
    ],
    hideKeyFrames: [
        { opacity: 1, transform: 'translateX(0%)' },
        { opacity: 0, transform: 'translateX(-100%)' },
    ],
    options: {
        duration: 1000,
        easing: 'ease-in-out'
    }
};

export enum AnimationType {
    Crossfade = 'crossfade',
    SlideRightToLeft = 'slide-right-to-left'
}
