import { LitElement, html, customElement, css, unsafeCSS, property, query } from 'lit-element';
import Style from './va-video-player.scss';

export const COMPONENT_NAME = 'va-video-player';
export const DEFAULT_SOURCE_TYPE = 'video/mp4';
export const DEFAULT_PRELOAD = 'metadata';
const DEFAULT_ASPECT_RATIO = '16-9';

/**
 * Video player component. 
 * Supports single video source that defaults to video/mp4.
 */
@customElement(COMPONENT_NAME)
export class VideoPlayer extends LitElement {
    /**
     * Specifies the URL of the video file.
     */
    @property({ type: String })
    public source = '';

    /**
     * Specifies the MIME-type of the resource.
     */
    @property({ type: String })
    public type = DEFAULT_SOURCE_TYPE;

    /**
     * Video aspect ratio.
     * @see _ratio.scss for available options
     */
    @property({ type: String })
    public aspectRatio = DEFAULT_ASPECT_RATIO;

    /**
     * Specifies that video controls should be displayed.
     */
    @property({ type: Boolean })
    public controls = false;

    /**
     * Specifies that the audio output of the video should be muted.
     */
    @property({ type: Boolean })
    public muted = false;

    /**
     * Specifies that the video will start playing as soon as it is ready and the element is visible.
     */
    @property({ type: Boolean })
    public autoplay = false;

    /**
     * Specifies how the video should be loaded when the page loads.
     */
    @property({ type: String })
    public preload = DEFAULT_PRELOAD;

    /**
     * Instance of the video element.
     */
    @query('video')
    video: HTMLVideoElement;

    /**
     * Intersection observer to apply play/stop video functionality when component visibility changes.
     */
    private visibilityObserver = new IntersectionObserver(this._visibilityObserverHandler.bind(this), {
        threshold: 1
    });

    /**
     * Property that indicates if the video is playing (the HTMLVideoElement does not provide it directly)
     * @returns true if the video is playing, otherwise false.
     */
    get isPlaying() : boolean {
        return !!(this.video && this.video.currentTime > 0 && !this.video.paused && !this.video.ended && this.video.readyState > HTMLMediaElement.HAVE_CURRENT_DATA);
    }


    /**
     * Intersection observer handler to control play/stop functionality when player visibility changes.
     * @param entries - intersectable entries
     */
    private _visibilityObserverHandler(entries: Array<IntersectionObserverEntry>): void {
        entries.forEach(entry => {
            if (this.autoplay && entry.isIntersecting && !this.isPlaying) {
                const promise = this.video.play();

                if (promise && !this.muted) {
                    //retry to play with video muted because some browsers only allow it with this setting 
                    promise.catch(e => {
                        this.video.muted = true;
                        this.video.play();
                    });
                }
            } else if (!entry.isIntersecting && this.isPlaying) {
                this.video.pause();
            }
        });
    }

    /**
     * Override firstUpdated to perform one-time work after the template has been created so the video element is available.
     * Muted property is affected here due to video reproduction done programatically and also due to some user agents wait 
     * for a user gesture to allow video reproduction (until then the video is unaware that it is muted).
     */
    firstUpdated() : void {
        this.visibilityObserver.observe(this.video);
        this.video.muted = this.muted;
    }

    render() {
        return html`
        <div class="ratio ratio--${this.aspectRatio}">
            <video ?controls="${this.controls}" ?muted="${this.muted}" preload="${this.preload}" class="ratio__item" playsinline>
                <source src="${this.source}" type="${this.type}">
            </video>
        </div>
        `;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
