import Style from './va-button.scss';
import { LitElement, html, property, customElement, css, unsafeCSS, TemplateResult, CSSResult } from 'lit-element';

/**
 * The tagname of the current component
 */
export const COMPONENT_NAME = 'va-button';

/**
 * A button that when clicked will have a loading state until it's manually setted back to initial state.
 * This WebComp doesn't use the shadow DOM to let the button inherit the styles from the page, according to
 * the passed class, but also to let the potential type="submit" submit the surrounding form.
 */
@customElement(COMPONENT_NAME)
export class VaButton extends LitElement {
    /**
     * The text to set in the button element
     */
    @property({ type: String })
    text = '';

    /**
     * This is the type that has to be set on the encapsulated <button> tag
     * Valid values are the one available for the "<button type="XXX"> attribute
     */
    @property({ type: String, attribute: 'type' })
    type = 'button';

    /**
     * This is the css class that have to be placed on the encapsulated <button> tag
     */
    @property({ type: String, attribute: 'btn-class' })
    btnClass = '';

    /**
     * Define if the button is in a loading state
     */
    @property({ type: Boolean, attribute: 'is-loading' })
    isLoading = false;

    /**
     * Override the color of the icon's outer line
     */
    @property({ type: String, attribute: 'icon-color' })
    iconColor: string;

    /**
     * Inform whether the loading state should be triggered manually by the consumner
     * or if we set the loading state automatically when the button is clicked.
     */
    @property({ type: Boolean, attribute: 'manual-state' })
    isManualState = false;

    /**
     * This prevent the use of the shadowDom by returning
     * directly the instance instead of a shadow root
     * @override
     * @returns the current instance of the class
     */
    createRenderRoot(): VaButton {
        return this;
    }

    /**
     * Toggle the isLoading state or force to a given one if the parameter is passed
     * @param shouldForceLoadingState - the forced state to set. If undefined, state is toggled
     */
    toggleLoadingState(shouldForceLoadingState?: boolean): void {
        if (typeof shouldForceLoadingState !== 'boolean') {
            shouldForceLoadingState = !this.isLoading;
        }

        this.isLoading = shouldForceLoadingState;
    }

    /**
     * The behavior to trigger when the button is clicked. If not isManualState, the click will
     * will also set the loading state to true
     * @param evt - the event triggered by the button
     */
    onButtonClick(evt: Event): void {
        // as long as it's in the loading state we prevent every action to be dispatched
        if (this.isLoading) {
            evt.stopPropagation();
            evt.preventDefault();
            return;
        }

        if (!this.isManualState) {
            this.toggleLoadingState(true);
        }
    }

    /**
     * This is a loading icon template
     * @param isLoading - inform whether the component is in loading state or not
     * @param customIconColor - a custom color for the outer line of the icon
     * @returns the compiled template of the loading icon
     */
    getLoadingIcon(isLoading: boolean, customIconColor: string): TemplateResult {
        if (!isLoading) {
            return null;
        }

        return html`
            <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="20px" height="20px"
                viewBox="0 0 80 80" preserveAspectRatio="xMidYMid" class="va-button__icon">
                <circle class="va-button__circle" cx="40" cy="40" r="30" style="${customIconColor ? 'stroke:' + customIconColor : ''}">
                    <animateTransform attributeName="transform" type="rotate" dur="2s" repeatCount="indefinite" keyTimes="0;1"
                        values="0 40 40;360 40 40"></animateTransform>
                </circle>
            </svg>
        `;
    }

    /**
     * @override
     */
    render(): TemplateResult {
        return html`
            <button class="va-button ${this.isLoading ? 'is-loading' : ''} ${this.btnClass}" type="${this.type}" @click="${this.onButtonClick}">
                <span class="va-button__text">${this.text}</span>
                ${this.getLoadingIcon(this.isLoading, this.iconColor)}
            </button>
            <style class="d-none">${VaButton.styles}</style>
        `;
    }

    /**
     * Css styles
     * @returns the CSS styles
     */
    static get styles(): Array<CSSResult> {
        return [css`${unsafeCSS(Style)}`];
    }
}
