import { customElement, html, css, unsafeCSS, property, TemplateResult } from 'lit-element';
import { SlideshowIndicator } from './slideshow-indicator';
import StyleBase from './slideshow-indicator-base.scss';
import Style from './slideshow-text-indicator.scss';

export const COMPONENT_NAME = 'va-slideshow-text-indicator';

const indicatorTextClassName = 'carousel__indicator-text';
const indicatorIconClassName = 'carousel__indicator-icon';
const indicatorIconContent = ''
const indicatorItemModifierClassName = 'carousel__indicator-item--icon-marker';

/**
 * Slideshow text indicator that has the default behaviour on mobile, 
 * but on desktop displays text and a icon as the list item marker.
 */
@customElement(COMPONENT_NAME)
export class SlideshowTextIndicator extends SlideshowIndicator {

    @property( { type: String })
    public text = '';

    get indicatorContentHtml() : TemplateResult {
        return html`<i class="${indicatorIconClassName}">${indicatorIconContent}</i><span class="${indicatorTextClassName}">${this.text}</span>`;
    }

    render(): TemplateResult {
        return html`${this.baseHtml(this.isActive, indicatorItemModifierClassName, this.indicatorContentHtml)}`;
    }

    static get styles() {
        return [css`${unsafeCSS(StyleBase)} ${unsafeCSS(Style)}`];
    }
}
