import { customElement } from 'lit-element';
import { DEFAULT_SLIDE_ANIMATION_DURATION, SlideshowItem } from './slideshow-item';

export const COMPONENT_NAME = 'va-slideshow-text';

@customElement(COMPONENT_NAME)
export class SlideshowTextItem extends SlideshowItem {

    /**
     * Initializes this slide.
     */
    public initializeSlide(): void {
        super.initializeSlide();

        if (this.isActive) {
            this.dispatchSetSlideAnimationTimerEvent(DEFAULT_SLIDE_ANIMATION_DURATION);
        }
    }

    /**
     * Handler for the animation finish event.
     * Remove animation class from the slide and dispatches a new set slide animation event.
     * @param animation Animation instance
     */
    onFinishShowAnimationHandler(animation: Animation): void {
        super.onFinishShowAnimationHandler(animation);
        this.dispatchSetSlideAnimationTimerEvent(DEFAULT_SLIDE_ANIMATION_DURATION);
    }
}
