import { mvcValidationDataAttributes } from '../mvc-validation-data-attribute';
import { ValidatableElement } from '../validatable-element';
import { checkRequiredValidation } from '../functions/required-validation';
import { checkEmailValidation } from '../functions/email-validation';
import { checkDateIsInRange, checkDateIsValid } from '../functions/date-validation';

export type ValidatorFn = (_el: ValidatableElement, _formElement?: HTMLFormElement) => string;

/**
 * This class is responsible to bind a form element to a custom
 * validator
 */
export class FormValidator {
    /**
     * The saved instance for singleton pattern
     */
    private static instance: FormValidator;

    /**
     * Contains all registered validators
     */
    validatorsRegister: { [key: string]: ValidatorFn } = {};

    /**
     * Create an instance of FormValidator
     * Use getInstance() for singleton creation
     * Ctor is public for non singleton creation and for tests
     */
    public constructor() {
        this._initValidators();
    }

    /**
     * This method returns a singleton instance of the class
     * @returns - the singleton instance of FormValidator
     */
    public static getInstance(): FormValidator {
        if (!FormValidator.instance) {
            FormValidator.instance = new FormValidator();
        }

        return FormValidator.instance;
    }

    /**
     * Run the validation on a field by checking the attributes and running the validation
     * @param formControl - the form field to validate
     * @param formElement - the form tag in which the control is included in. This param is necessary for radio and checkboxes
     * @returns - the error message if any, or an empty string
     */
    runValidationForField(formControl: ValidatableElement, formElement?: HTMLFormElement): string {
        let errorMsg = '';

        if (!this.shouldValidate(formControl)) {
            return errorMsg;
        }

        // run the validation one by one
        for (const attributKey in formControl.dataset) {
            if (!(attributKey in mvcValidationDataAttributes && attributKey !== 'val')) {
                // it's not a validation data attribute
                continue;
            }

            const key = attributKey as keyof typeof mvcValidationDataAttributes;
            const validationName = mvcValidationDataAttributes[key];
            const validationFn = this.validatorsRegister[validationName];
            if (typeof validationFn !== 'function') {
                console.debug(`ValidationFn was not registered for ${attributKey}`);
                continue;
            }

            errorMsg = validationFn(formControl, formElement);

            // Once we have an error we don't need to go further
            if (errorMsg.length) {
                break;
            }
        }
        return errorMsg;
    }

    /**
     * Define if the element should be validated or not.
     * If the field has no 'data-val' attribute, we don't validate it
     * @param el - the form control to validate
     * @returns - a boolean informing whether we should validate or not
     */
    shouldValidate(el: ValidatableElement): boolean {
        return 'val' in el.dataset;
    }

    /**
     * Method to register a validator in the manager
     * @param name - the name of the validator which is the data attribute used for activation
     * @param validatorFn - the validator function to use to validate the field
     * @returns - the ValidationHandler instance for chaining
     */
    registerValidator(name: string, validatorFn: ValidatorFn): FormValidator {
        if (this.validatorsRegister[name]) {
            console.error(`Name conflicts. The validator ${name} is already registered.`);
        } else {
            this.validatorsRegister[name] = validatorFn;
        }

        return this;
    }

    /**
     * Initialize the existing validators that can be used from
     * the asp side
     */
    private _initValidators(): void {
        this.registerValidator(mvcValidationDataAttributes.valRequired, checkRequiredValidation)
            .registerValidator(mvcValidationDataAttributes.valDate, checkDateIsValid)
            .registerValidator(mvcValidationDataAttributes.valEmail, checkEmailValidation)
            .registerValidator(mvcValidationDataAttributes.valDatetimerangevalidation, checkDateIsInRange);
    }
}
