const standardFileDownloadRegex = new RegExp(/\.(pdf|xls|doc|png|jpg).*/i);
const defaultSfDownloadLinks = `a[href*='/docs/'], a[href*='/images/']`;

/**
 * Gets file name from standard Sitefinity document download url
 * @param {HTMLAnchorElement} element Link element
 * @returns {string} File name if is valid Sitefinity document download url, "null" otherwise
 */
export function getFileNameFromLinkUrl(element: HTMLAnchorElement): string {
    const url = element.getAttribute('href');
    let fileName: string = null;

    if (element.matches(defaultSfDownloadLinks) || url && url.match(standardFileDownloadRegex)) {
        fileName = element.dataset.fileName || element.getAttribute('title') || element.innerText || null;
    }

    return fileName;
}
