/**
 * Validate an email by creating a in memory email field and using
 * the browser's built-in validation process
 * @param email - the email to validate
 * @returns a boolean informing whether the field is valid or not
 */
export function isEmailValid(email: string): boolean {
    const field = document.createElement('input');
    field.type = 'email';
    field.value = email;

    return field.validity.valid && !field.validity.typeMismatch;
}
