import { GoogleTagManagerService, emptyLabel } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { ExtendedWindow } from '../../models/extended-window.model';

declare const window: ExtendedWindow;
const gtmService = GoogleTagManagerService.getInstance();

/**
 * Registers event callbacks for tracking Contact Form events
 */
export function trackContactForm(): void {
    const contactForm = document.querySelector('.va-contact-form');

    if (contactForm) {
        gtmService.pushEvent(GTMEvent.ContactForm, GTMCategory.ClientEnquiries, 'Contact form: Start', emptyLabel);

        contactForm.addEventListener('submit', () => {
            if (!window.grecaptcha || window.grecaptcha.getResponse()) {
                gtmService.pushEvent(GTMEvent.ContactForm, GTMCategory.ClientEnquiries, 'Contact form: Complete', emptyLabel);
            }
        });
    }
}
