import { Breakpoints } from '../constants/breakpoints';

const orderedBreakpoints: Array<Breakpoint> = [
    {
        name: 'xl',
        min: Breakpoints.xl,
        max: null
    },
    {
        name: 'lg',
        min: Breakpoints.lg,
        max: Breakpoints.xl
    },
    {
        name: 'md',
        min: Breakpoints.md,
        max: Breakpoints.lg
    },
    {
        name: 'sm',
        min: Breakpoints.sm,
        max: Breakpoints.md
    },
    {
        name: 'xs',
        min: Breakpoints.xs,
        max: Breakpoints.sm
    }
];

/**
 * Represents a screen breakpoint definition
 */
export interface Breakpoint {
    name: string;
    min: number;
    max: number | null;
}

/**
 * Gets current breakpoint from window viewport
 * @returns Current breakpoint
 */
export function getCurrentBreakpoint(): Breakpoint {
    var windowWidth = window.innerWidth;

    for (let i = 0; i < orderedBreakpoints.length; ++i) {
        const breakpoint = orderedBreakpoints[i];

        if (windowWidth >= breakpoint.min) {
            return breakpoint;
        }
    }
}
