import { customElement, html, css, unsafeCSS, property, TemplateResult } from 'lit-element';
import { SlideshowIndicator } from './slideshow-indicator';
import StyleBase from './slideshow-indicator-base.scss';
import Style from './slideshow-thumbnail.scss';

export const COMPONENT_NAME = 'va-slideshow-thumbnail';

@customElement(COMPONENT_NAME)
export class SlideshowThumbnail extends SlideshowIndicator {

    @property({ type: String })
    public imageSource = '';

    @property({ type: String })
    public imageAlt = '';

    @property({ type: String})
    public text = '';

    render(): TemplateResult {
        const contentHtml = html`
            <span>
                <img src="${this.imageSource}" alt="${this.imageAlt}" class="carousel__indicator-thumbnail">
                <p class="carousel__indicator-text">${this.text}</p>
            </span>`;
        return html`${this.baseHtml(this.isActive, 'carousel__indicator-item--thumbnail', contentHtml)}`;
    }

    static get styles() {
        return [ css`${unsafeCSS(StyleBase)} ${unsafeCSS(Style)}` ];
    }
}
