//  This script has a dependency to google Maps api (window.google.maps)
// => you need to load https://maps.googleapis.com/maps/api/js?....

import { GoogleTagManagerService } from "../services/google-tag-manager/google-tag-manager-service";

const searchInputSelector: string = '.js-map-item-search';

/**
 * Option to restrict search to cities and postal codes
 */
const typesRestriction: string[] = ['(regions)'];

/**
 * Option to restrict search to swiss cities
 */
const countryRestriction: google.maps.places.ComponentRestrictions = { country: ['ch', 'li'] };

const options: google.maps.places.AutocompleteOptions = {
    types: typesRestriction,
    componentRestrictions: countryRestriction
};

/**
 * Initialize google autocomplete
 */
export function initLocationAutocomplete() {
    // Guard: to check if google Map Api is loaded
    if (window.google === undefined || window.google.maps === undefined) {
        console.warn('Google Maps Api is not loaded but is required by LocationAutocomplete');
        return;
    }

    const searchInputs: NodeListOf<Element> = document.querySelectorAll(searchInputSelector);

    searchInputs.forEach(element => {
        const inputElement: HTMLInputElement = element as HTMLInputElement;
        const resultsPage: string = inputElement.dataset.resultsPage;

        if (!resultsPage) {
            console.warn('Unable to get the result page in LocationAutocomplete');
            return;
        }

        const autocomplete: google.maps.places.Autocomplete = new google.maps.places.Autocomplete(inputElement, options);

        autocomplete.addListener("place_changed", () => {

            const newPlace: google.maps.places.PlaceResult = autocomplete.getPlace();

            if (newPlace.name.length) {
                // if user enters bullshit in the autocomplete => the getPlace() function returns an object with just 'name' defined
                // => checking geometry ensure we will have a position (and newPlace is legit, i.e. selected from the autocomplete propositions)
                if (newPlace.geometry) {
                    redirectToResultsPage(newPlace, resultsPage);
                } else {
                    getPlacePrediction(inputElement.value);
                }
            }
        });
    });
}

/**
 * Get a place prediction from google autocomplete
 * @param val user input
 */
function getPlacePrediction(val: string): void {
    // Uses Google's autocomplete service to select an address
    const autocompleteService: google.maps.places.AutocompleteService = new google.maps.places.AutocompleteService();

    autocompleteService.getPlacePredictions({
        input: val,
        types: typesRestriction,
        componentRestrictions: countryRestriction
    }, function (predictions: google.maps.places.AutocompletePrediction[], status: google.maps.places.PlacesServiceStatus) {

        if (status === google.maps.places.PlacesServiceStatus.OK && predictions.length) {
            const geocoder = new google.maps.Geocoder();

            geocoder.geocode({
                placeId: predictions[0].place_id
            }, function (data: google.maps.GeocoderResult[], status: google.maps.GeocoderStatus) {
                if (status === google.maps.GeocoderStatus.OK && data.length) {
                    const localityName = getAddress(data);
                    const inputElementList: NodeListOf<HTMLInputElement> = document.querySelectorAll(searchInputSelector);
                    if (inputElementList.length) {
                        inputElementList.forEach(inputElement => { inputElement.value = localityName; });
                    }
                }
            });
        }

    });
}

/**
 * Get an address from the search results
 *
 * @param searchResults all details about geocoder result
 * @returns the address if results page is not defined otherwise it will redirect to results page
*/
function getAddress(searchResults?: google.maps.GeocoderResult[]): string {
    const localities = searchResults.filter( ac => ac.types.indexOf('locality') >= 0 || ac.types.indexOf('postal_code') >= 0 );

    if (localities.length) {
        const address: string = localities[0].formatted_address;
        const inputElementList: NodeListOf<HTMLInputElement> = document.querySelectorAll(searchInputSelector);
        if (inputElementList.length) {
            inputElementList.forEach( inputElement => { inputElement.value = address; })
        }

        triggerSearchEvent(localities[0].geometry.location);
        return address;
    }
}

/**
 * Redirect to the location search page
 *
 * @param searchResults all details about place result
 * @param resultsPage uri of agency result destination page
*/
function redirectToResultsPage(searchResults?: google.maps.places.PlaceResult, resultsPage?: string): void {
    if (searchResults) {
        const googleTagManager = GoogleTagManagerService.getInstance();
        const address: string = searchResults.formatted_address;
        googleTagManager.pushAgentSearch(address);

        if (resultsPage) {
            const url: string = `${resultsPage}/?search=${address}`;

            triggerSearchEvent(searchResults.geometry.location);
            googleTagManager.pushContentCta(address, url);
            window.location.href = url;
        }
    } else {
        throw "Data or page destination empty";
    }
}

/**
 * Send event: new position searched
 *
 * @param position latitude and longitud data in degrees
 */
function triggerSearchEvent(position: google.maps.LatLng): void {
    document.addEventListener('map_item_search_changed', () => ({ newPlace: position }) );
}
