import { ValidatableElement } from './validatable-element';

/**
 * The error message keys. Those are used only if there was a validation error
 * but no error message was found in the attributes. In that case the missing message
 * would be evaluated as "there's no error" so we fallback to one of those error
 * constant to ensure that we always return an error message when the validation failed
 */
export const validationError = {
    error: '_validationError',
    dateIsInvalid: '_dateIsInvalid',
    dateNotInRange: '_dateIsNotInRange',
    emailIsInvalid: '_emailIsInvalid',
    isRequired: '_isRequired',
};

/**
 * Get the error message from the field or fallback to a standard message key
 * @param controls - the element on which the attribute should be set
 * @param attributeName - the name of the attribute containing the error message
 * @param fallbackMessage - the fallback message to use if the attribute is missing
 * @returns - the validation error message
 */
export function getErrorMessage(controls: ValidatableElement | RadioNodeList, attributeName: string, fallbackMessage: string): string {
    let errorMessage;
    if (controls instanceof RadioNodeList) {
        for (const el of controls) {
            // eslint-disable-next-line no-extra-parens -- reason: necessary for TS cast
            errorMessage = (el as HTMLInputElement).getAttribute(attributeName);
            if (errorMessage) {
                break;
            }
        }
    } else {
        errorMessage = controls.getAttribute(attributeName);
    }

    if (!errorMessage) {
        errorMessage = fallbackMessage ? fallbackMessage : validationError.error;
    }

    return errorMessage;
}
