/*
* Custom event names defined in a specific file, because it needs to be shared to calculator hooks.
* Due to unit-tests importing spec by spec, if this is in the same file as the WebComponent,
* the "customElement" decorator will try to register the WebComponent multiple times.
*/
export const SLIDER_CUSTOM_EVENT_NAMES = {
    SliderValueChanged: 'va-slider-change',
}
