import { customElement } from 'lit-element';
import { SlideshowItem, DEFAULT_SLIDE_ANIMATION_DURATION } from './slideshow-item';

export const COMPONENT_NAME = 'va-slideshow-video';
export const ADDITIONAL_ANIMATION_TIMER_DURATION = .5;

@customElement(COMPONENT_NAME)
export class SlideshowVideoItem extends SlideshowItem {

    _video!: HTMLVideoElement;

    /**
     * Checks if the video element is being displayed.
     * On some use cases, the video might be hidden on mobile.
     */
    private get isVideoElementDisplayed(): boolean {
        return this._video.offsetWidth > 0 && this._video.offsetHeight > 0;
    }

    /**
     * Retrieves the slide animation timer duration.
     * If video is displayed, an aditional amount is added to the video duration
     * to give time to the video to stop before the slide animation triggers.
     * Otherwise default animation timer is returned.
     */
    private get getSetSlideAnimationTimerDuration(): number {
        return this.isVideoElementDisplayed ?
            (this._video.duration + ADDITIONAL_ANIMATION_TIMER_DURATION) * 1000 :
            DEFAULT_SLIDE_ANIMATION_DURATION;
    }

    /**
     * Reproduces the slide media if it's displayed and sets and
     * dispatches event to set slide animation timer.
     * Force muted option due to user-agent compatability.
     */
    private reproduceSlideMedia() {
        if (this._video.HAVE_METADATA && this.isVideoElementDisplayed) {
            this._video.currentTime = 0;
            this._video.muted = true;
            this._video.play();
        }

        this.dispatchSetSlideAnimationTimerEvent(this.getSetSlideAnimationTimerDuration);
    }

    /**
     * On video loaded metadata event handler.
     * If this slide is the active one reproduce media.
     */
    private onLoadedMetadata(): void {
        if (this.isActive) {
            this.reproduceSlideMedia();
        }
    }

    /**
     * Initializes this slide.
     * Dispatch event to set slideshow timer if video metadata is available.
     */
    public initializeSlide(): void {
        super.initializeSlide();

        // get the <video> element in the <slot>
        // https://lit-element.polymer-project.org/guide/templates#accessing-slotted-children
        const slot = this.shadowRoot.querySelector('slot');
        const childNodes = slot.assignedNodes({ flatten: true });

        childNodes.forEach((node: Node) => {
            if (node.nodeType === Node.ELEMENT_NODE) {
                const nodeElem = node as HTMLElement;
                const videosArray = nodeElem.getElementsByTagName('video');

                if (videosArray.length > 0) {
                    this._video = videosArray[0];
                }
                else {
                    throw 'Not configured correctly';
                }
            }
        });


        if (!this._video.HAVE_METADATA) {
            this._video.addEventListener('loadedmetadata', this.onLoadedMetadata.bind(this));
        }

        if (this.isActive) {
            this.reproduceSlideMedia();
        }
    }

    /**
     * Handler for the animation finish event.
     * Remove animation class from the slide and reproduces the slide media.
     * @param animation Animation instance
     */
    onFinishShowAnimationHandler(animation: Animation): void {
        super.onFinishShowAnimationHandler(animation);
        this.reproduceSlideMedia();
    }
}
