import { LitElement, html, css, unsafeCSS, customElement, queryAssignedNodes, property } from 'lit-element';
import { SlideshowItem } from './slideshow-item';
import { SlideshowThumbnail } from './slideshow-thumbnail';
import { SwipeEventsGenerator } from '../../cms/scripts/events/swipe-events';
import { SLIDESHOW_CUSTOM_EVENT_NAMES } from './custom-event-names';
import Style from './slideshow.scss';
import { crossfadeAnimationSettings, AnimationType, slideAnimationSettings } from './animation-effects';
import { AnimationSettings } from './animation-settings';
import { SlideshowLayoutType } from './slideshow-layout-type';
import { IndicatorsListLayoutType } from './indicators-list-layout-type';

export const COMPONENT_NAME = 'va-slideshow';
export const SLIDES_SLOT_NAME = 'slides';
export const INDICATORS_SLOT_NAME = 'indicators';
const WINDOW_RESIZE_THROTTLING_DELAY = 150;

@customElement(COMPONENT_NAME)
export class Slideshow extends LitElement {
    /**
     * Slider animation timer.
     */
    private slideAnimationTimer: number;

    /**
     * Active slide index.
     */
    private activeSlideIndex = 0;

    /**
     * Total number of slides.
     */
    private totalSlidesCount = 0;

    /**
     * Collection of the slideshow slide items.
     */
    @queryAssignedNodes(SLIDES_SLOT_NAME)
    _slides!: NodeListOf<SlideshowItem>;

    /**
     * Collection of slideshow indicators.
     */
    @queryAssignedNodes(INDICATORS_SLOT_NAME)
    _indicators!: NodeListOf<SlideshowThumbnail>;

    /**
     * The property that sets the effect to apply on transitions
     */
    @property({ type: String })
    effect: string = AnimationType.Crossfade;

    /**
     * The property that indicates if the transition is automatic
     */
    @property({ type: Boolean })
    autoTransition: boolean;

    /**
     * Slideshow layout
     */
    @property({ type: SlideshowLayoutType })
    layout = SlideshowLayoutType.Default;

    /**
     * Slideshow indicators list type.
     */
    @property({ type: IndicatorsListLayoutType })
    indicatorsLayout = IndicatorsListLayoutType.Default;

    /**
     * Slideshow indicators list type.
     */
    @property({ type: String })
    indicatorsCssClass: string = '';

    /**
     * The property that indicates max slide height among all existing slides
     */
    @property({ type: String })
    private maxSlideHeight: string = 'auto';

    /**
     * Time out Id to resize height of carousel to avoid scroll flickering
     */
    private resizeTimeoutId = 0;

    get animationSettings(): AnimationSettings {
        return this.effect === AnimationType.SlideRightToLeft
            ? slideAnimationSettings
            : crossfadeAnimationSettings;
    }

    get slideshowLayoutCssClassModifier(): string {
        return this.layout !== SlideshowLayoutType.Default ?
            `carousel--${this.layout}` : '';
    }

    get indicatorsListCssClassModifier(): string {
        return this.indicatorsLayout !== IndicatorsListLayoutType.Default ? `carousel__indicators-list--${this.indicatorsLayout}` : '';
    }

    /**
     * Hides the active slide and shows the specified one if the indexes are diferent
     * and none of the slides are running the animation.
     * @param slideToShowIndex index of the slide to display.
     * @param automatic Indicates whether this was caused by automatic animation or by user click
     */
    private displaySlide(slideToShowIndex: number, automatic: boolean = false): void {
        if (this.activeSlideIndex !== slideToShowIndex) {
            let activeSlide = this._slides[this.activeSlideIndex];
            let slideToShow = this._slides[slideToShowIndex];

            if (!activeSlide.isRunningAnimation() && !slideToShow.isRunningAnimation()) {
                this.clearSlideAnimationTimer();

                activeSlide.hide(this.animationSettings);
                slideToShow.show(this.animationSettings);

                this._indicators[this.activeSlideIndex].isActive = false;
                this._indicators[slideToShowIndex].isActive = true;

                this.activeSlideIndex = slideToShowIndex;

                this.dispatchEvent(new CustomEvent(SLIDESHOW_CUSTOM_EVENT_NAMES.SlideChanged, {
                    bubbles: true,
                    cancelable: true,
                    composed: true,
                    detail: {
                        title: slideToShow.title,
                        automatic
                    }
                }));

            }
        }
    }

    /**
     * OnClick event handler for the previous slide navigation arrow
     */
    private showPreviousSlide(): void {
        this.displaySlide(this.getPreviousSlideIndex());
    }

    /**
     * Displays the next slide
     * @param automatic Indicates whether this was caused by automatic animation or by user click
     */
    private showNextSlide(automatic: boolean = false): void {
        this.displaySlide(this.getNextSlideIndex(), automatic);
    }

    /**
     * Returns the index of the next slide to be displayed.
     */
    private getNextSlideIndex(): number {
        return this.activeSlideIndex + 1 < this.totalSlidesCount ? this.activeSlideIndex + 1 : 0;
    }

    /**
     * Returns the index of the previous slide to be displayed.
     */
    private getPreviousSlideIndex(): number {
        return this.activeSlideIndex - 1 < 0 ? this.totalSlidesCount - 1 : this.activeSlideIndex - 1;
    }

    /**
     * Sets the slideshow animation timed event
     * @param {number} slideAnimationTimeout Timer duration
     */
    private setSlideAnimationTimer(slideAnimationTimeout: number): void {
        this.clearSlideAnimationTimer();
        this.slideAnimationTimer = window.setTimeout(() => this.showNextSlide(true), slideAnimationTimeout);
    }

    /**
     * Clear the slide animation timer if already  set.
     */
    private clearSlideAnimationTimer(): void {
        if (this.slideAnimationTimer) {
            window.clearTimeout(this.slideAnimationTimer);
        }
    }

    /**
     * Custom event handler to set timed slide event with the required slide timeout duration.
     * @param event set slide animation timer custom event
     */
    private setSlideAnimationTimerHandler(event: CustomEvent): void {
        if (this.autoTransition) {
            this.setSlideAnimationTimer(event.detail.duration);
        }
    }

    /**
     * Initialize indicators click event handlers
     */
    private initializeIndicators(): void {
        this._indicators.forEach((item, index) => {
            item.addEventListener('click', () => this.displaySlide(index));
        });
    }

    async firstUpdated() {
        this.totalSlidesCount = this._slides.length;

        if (this.totalSlidesCount > 1) {
            SwipeEventsGenerator.initializeGenerator();
        }

        this.addEventListener(SLIDESHOW_CUSTOM_EVENT_NAMES.SetSlideAnimationTimer, this.setSlideAnimationTimerHandler);
        this.addEventListener(SLIDESHOW_CUSTOM_EVENT_NAMES.ShowPreviousSlide, this.showPreviousSlide);
        this.addEventListener(SLIDESHOW_CUSTOM_EVENT_NAMES.ShowNextSlide, () => this.showNextSlide());

        this.initializeIndicators();

        await Promise.all(Array.from(this._slides).map((c) => c.updateComplete));

        this.computeSlideShowHeight();
    }

    connectedCallback() {
        super.connectedCallback();
        window.addEventListener('resize', this.onWindowResize.bind(this));
    }

    disconnectedCallback() {
        window.removeEventListener('resize', this.onWindowResize);
        super.disconnectedCallback();
    }

    /**
     * Function that handles and throttles window resizes */
    private onWindowResize() {

        if (this.resizeTimeoutId) {
            window.clearTimeout(this.resizeTimeoutId);
        }

        this.resizeTimeoutId = window.setTimeout(() => {
            this.computeSlideShowHeight();
            this.resizeTimeoutId = null;
        }, WINDOW_RESIZE_THROTTLING_DELAY);
    }

    /**
     * Computes min slide show height
     */
    private computeSlideShowHeight(): void {
        var slidesMaxHeight = this.getSlidesMaxHeight();

        if (this.maxSlideHeight !== slidesMaxHeight) {
            this.maxSlideHeight = slidesMaxHeight;
        }
    }

    /**
     * Gets slides max height represented on px units
     */
    private getSlidesMaxHeight(): string {
        var maxHeight = -1;

        this._slides.forEach(s => {
            maxHeight = Math.max(maxHeight, s.getHeight());
        });

        return `${maxHeight}px`;
    }

    render() {
        return html`
            <section class="carousel ${this.slideshowLayoutCssClassModifier}">
                <div class="carousel__items" style="min-height: ${this.maxSlideHeight}">
                    <slot name="${SLIDES_SLOT_NAME}"></slot>
                </div>
                <div class="carousel__indicators">
                    <ol class="carousel__indicators-list ${this.indicatorsListCssClassModifier} ${this.indicatorsCssClass}">
                        <slot name="${INDICATORS_SLOT_NAME}"></slot>
                    </ol>
                </div>
            </section>
        `;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
