import { validationError, getErrorMessage } from '../validation-error';
import { mvcValidationDataAttributes } from '../mvc-validation-data-attribute';
import { ValidatableElement } from '../validatable-element';
import { isEmailValid } from '../../validators/email/email-validator';

/**
 * Check if the passed field pass the email validation
 * @param _el - the form field to validate
 * @returns - an error message if there was an error or an empty string
 */
export function checkEmailValidation(_el: ValidatableElement): string {
    const currentValue = _el.value;

    if (isEmailValid(currentValue)) {
        return '';
    }

    return getErrorMessage(_el, mvcValidationDataAttributes.valEmail, validationError.emailIsInvalid);
}
