import { GoogleTagManagerService } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';

const CostCalculatorClassName = 'js-gtm-cost-calculator';

/**
 * Hooks click events to a specific calculator element, in order to trigger GTM event
 * @param calculator - the calculator widget
 */
function hookEvents(calculator: HTMLElement): void {
    const elements = calculator.querySelectorAll(`input, select, va-slider`);
    const gtmService = GoogleTagManagerService.getInstance();

    elements.forEach(element => {
        element.addEventListener('click', _evt => {
            if (!calculator.dataset.hasHandledCostCalculatorEvent) {
                gtmService.pushEvent(GTMEvent.CostCalculator, GTMCategory.ContentEngagement, 'Calculator', gtmService.gtmVariables.product);
                calculator.dataset.hasHandledCostCalculatorEvent = true.toString();
            }
        });
    });
}

/**
 * Tracks event related to cost calculator widgets
 */
export function trackCostCalculators(): void {
    const elementsToBeTracked = document.querySelectorAll(`.${CostCalculatorClassName}`);
    elementsToBeTracked.forEach(hookEvents);
}

/**
 * Tracks event related to a specific cost calculator widget
 * @param calculator - the calculator widget
 */
export function trackCostCalculator(calculator: HTMLElement): void {
    if (!calculator) {
        return;
    }

    hookEvents(calculator);
}

