import { ValidatableElement } from './validatable-element';

/**
 * Convert the asp 'required' data-attribute to a native HTML5 required
 * attribut as it's used for accessibility purpose but also for easy validation
 * @param element - the element on which to do the conversion
 */
function convertRequiredAttribute(element: ValidatableElement) {
    if (element.dataset.valRequired) {
        element.setAttribute('required', 'required');
        element.setAttribute('aria-required', 'true');
    }
}


/**
 * This method will convert asp data- attribute used for validation to native
 * html5 validation tags for accessibility and to use native validation method.
 * @param element - the form element (input, select, ...) to validate
 */
export function convertAspValidationTagsToHtml5(element: ValidatableElement): void {
    convertRequiredAttribute(element);
}
