import { css, customElement, html, LitElement, property, TemplateResult, unsafeCSS } from 'lit-element';
import Style from './va-tooltip.scss';

const IS_HIDDEN_CLASS = `is-hidden`;

/**
 * A component to represent an element tooltip
 */
@customElement('va-tooltip')
export class VaTooltip extends LitElement {
    @property({ type: String })
    public text = '';

    @property({ type: String })
    public for = '';

    @property({ type: Boolean })
    public isVisible = false;

    showTooltip(event: MouseEvent): void {
        if (event) {
            event.preventDefault();
            event.stopImmediatePropagation();
        }
        this.isVisible = true;
    }

    hideTooltip(): void {
        this.isVisible = false;
    }

    private get targetElement(): HTMLElement {
        return document.getElementById(this.for) as HTMLElement;
    }

    connectedCallback(): void {
        super.connectedCallback();

        if (this.targetElement) {
            this.targetElement.addEventListener('click', this.showTooltip.bind(this));
            this.targetElement.addEventListener('mouseover', this.showTooltip.bind(this));
            this.targetElement.addEventListener('mouseout', this.hideTooltip.bind(this));
            document.addEventListener('click', this.hideTooltip.bind(this));
        }
    }

    disconnectedCallback(): void {
        super.disconnectedCallback();

        if (this.targetElement) {
            this.targetElement.removeEventListener('click', this.showTooltip.bind(this));
            this.targetElement.removeEventListener('mouseover', this.showTooltip.bind(this));
            this.targetElement.removeEventListener('mouseout', this.hideTooltip.bind(this));
            document.removeEventListener('click', this.hideTooltip.bind(this));
        }
    }

    /**
     * @override
     */
    render(): TemplateResult {
        return html`<div class="tooltip ${this.isVisible ? '' : IS_HIDDEN_CLASS}">${this.text}<div>`;
    }

    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
