import { Breakpoints } from "../constants/breakpoints";
import { Breakpoint, getCurrentBreakpoint } from "../helpers/breakpoints";

const dynamicGridClassName = 'dynamic-custom-border-grid';
const customBorderClassName = 'custom-border';
const customBorderRightClassName = `${customBorderClassName}__right`;
const customBorderBottomClassName = `${customBorderClassName}__bottom`;

const configuration: { [id: string]: number; } = {
    md: 2,
    lg: 3,
    minWidth: Breakpoints.md
};

let currentBreakpoint: Breakpoint;

/**
 * Initializes dynamic custom border grids
 * */
export function initDynamicCustomBorderGrid(): void {
    const grids = document.querySelectorAll(`.${dynamicGridClassName}`);

    if (grids.length > 0) {
        currentBreakpoint = getCurrentBreakpoint();

        grids.forEach((grid: HTMLElement) => addCustomBorders(grid, currentBreakpoint));

        window.addEventListener('resize', onWindowResize);
    }
}

/**
 * Callback invoked when window resizes. Regenerates custom borders if screen breakpoint has changed.
 */
function onWindowResize(): void {
    const windowWidth = window.innerWidth;

    if (windowWidth < currentBreakpoint.min || (currentBreakpoint.max && windowWidth >= currentBreakpoint.max)) {
        currentBreakpoint = getCurrentBreakpoint();

        if (window.innerWidth >= configuration.minWidth) {
            document.querySelectorAll(`.${dynamicGridClassName}`).forEach((grid: HTMLElement) => {
                removeCustomBorders(grid);
                addCustomBorders(grid, currentBreakpoint);
            });
        }
    }
}

/**
 * Removes all custom borders
 * @param grid Current grid
 */
function removeCustomBorders(grid: Element): void {
    grid.querySelectorAll(`.${customBorderRightClassName}, .${customBorderBottomClassName}`).forEach(e => e.parentNode.removeChild(e));
}

/**
 * Adds custom borders based on grid layout for the provided breakpoint
 * @param grid Current grid
 * @param breakpoint Current breakpoint
 */
function addCustomBorders(grid: Element, breakpoint: Breakpoint): void {
    const customBorders = grid.querySelectorAll(`.${customBorderClassName}`);
    const gridLayout = getGridLayout(customBorders, breakpoint);

    for (let i = 0; i < customBorders.length; ++i) {
        const customBorder = customBorders[i];

        //Add right border to all elements except the last one on the row
        if (i % gridLayout.cols < gridLayout.cols - 1) {
            const right = document.createElement('div');
            right.classList.add(customBorderRightClassName);

            customBorder.appendChild(right);
        }

        //Add bottom border to all elements except the items on the last row
        if (i / gridLayout.cols < gridLayout.rows - 1) {
            const bottom = document.createElement('div');
            bottom.classList.add(customBorderBottomClassName);

            customBorder.appendChild(bottom);
        }
    }
}

/**
 * Gets the grid layout based on provided breakpoint
 * @param customBorders
 * @param breakpoint
 */
function getGridLayout(customBorders: NodeListOf<Element>, breakpoint: Breakpoint): { cols: number, rows: number } {
    const cols = configuration[breakpoint.name] || configuration.lg;
    const rows = Math.ceil((customBorders.length - 1) / cols);

    return {
        cols,
        rows
    };
}
