import { ExtendedWindow } from '../../models/extended-window.model';
import { classNames } from '../../constants/class-names';

declare const window: ExtendedWindow;

/**
 * The class name of the google recaptcha in automatic render mode
 */
const googleRecaptchaSelector = '.g-recaptcha';

/**
 * The attribute name of the google recaptcha id
 */
const googleRecaptchaIdAttribute = 'data-recaptcha-id';

/**
 * The class name of the recaptcha component warning messages
 */
const recaptchaErrorMessageSelector = `.recaptcha .${classNames.validationMessage}`;

/**
 * Function used as callback by google recaptcha, when the element is attached to DOM
 */
export function initRecaptcha(): void {
    const recaptchas = document.querySelectorAll(googleRecaptchaSelector) as NodeListOf<HTMLDivElement>;

    recaptchas.forEach((recaptcha) => {
        if (!recaptcha.hasAttribute(googleRecaptchaIdAttribute)) {
            recaptcha.dataset.recaptchaId = window.grecaptcha.render(recaptcha, {
                sitekey: recaptcha.dataset.sitekey
            });

            const captchaErrorMessage = recaptcha.parentElement.querySelector(recaptchaErrorMessageSelector);
            captchaErrorMessage?.classList.add(classNames.isHidden);
        }
    });
}

/**
 * Validate that a Recaptcha has been successfully completed
 *
 * @param event - The Form submit event
 * @returns True, if the Recaptcha is valid
 */
export function validateRecaptcha(event: Event): boolean {
    const parentFormElement = event.target as HTMLElement;
    const recaptcha = parentFormElement.querySelector(googleRecaptchaSelector);
    const captchaId = recaptcha.getAttribute(googleRecaptchaIdAttribute);
    const captchaErrorMessage = parentFormElement.querySelector(recaptchaErrorMessageSelector);
    let isRecaptchaValid = false;

    // Google assigns automatic id's, starting with 0. Thus, the condition over 'undefined', to avoid false positives
    if (typeof captchaId !== 'undefined') {
        const response = window.grecaptcha.getResponse(captchaId);
        isRecaptchaValid = Boolean(response.length);
        if (!isRecaptchaValid) {
            event.preventDefault();
            event.stopImmediatePropagation();
            captchaErrorMessage.classList.remove(classNames.isHidden);
        } else {
            captchaErrorMessage.classList.add(classNames.isHidden);
        }
    }

    return isRecaptchaValid;
}

/**
 * Toggle the visibility of the Recaptcha Elements (recapctha + error message).
 * Error message will be toggled only when hiding the elements. Prevent to display the message by default.
 * You can force the toggle to a specific visibility using method parameter
 *
 * @param parentFormElement - The Form Element where the Recaptcha is displayed
 * @param forceVisibilityTo - undefined will toggle the element, if set to true will force to show the element, if set to false will force to hide the element
 * @returns The final visibility of the element: true => element becomes visible, false => hidden
 */
export function toggleRecaptchaElements(parentFormElement: HTMLFormElement, forceVisibilityTo?: boolean): boolean {
    const recaptcha = parentFormElement.querySelector(googleRecaptchaSelector) as HTMLDivElement;
    const captchaErrorMessage = parentFormElement.querySelector(recaptchaErrorMessageSelector) as HTMLDivElement;
    const shouldHide = true; // add the hide class to hide the element
    const shouldShow = false;
    let shouldAddClass = shouldHide;

    // check in priority if the visibility should be force to a specific status (forceVisibilityTo)
    if (forceVisibilityTo === true) {
        shouldAddClass = shouldShow;
    } else if (forceVisibilityTo === false) {
        shouldAddClass = shouldHide;
    } else if (recaptcha.classList.contains(classNames.isHidden)) {
        // toggle class (by default the action is HIDE_ACTION so no need to set it)
        shouldAddClass = shouldShow;
    }

    // toggle class elements
    recaptcha?.classList.toggle(classNames.isHidden, shouldAddClass);
    if (shouldAddClass === shouldHide) { // toggle the error message only if it is to hide it, don't display the error msg by default
        captchaErrorMessage?.classList.toggle(classNames.isHidden, shouldAddClass);
    }

    return shouldAddClass === shouldShow;
}
