import { GoogleTagManagerService } from '../../cms/scripts/services/google-tag-manager/google-tag-manager-service';
import { LitElement, html, property, customElement, queryAssignedNodes } from 'lit-element';
import { GTMEvent } from '../../cms/scripts/models/google-tag-manager/gtm-event';
import { GTMCategory } from '../../cms/scripts/models/google-tag-manager/gtm-category';

const IS_ACTIVE_CLASS_NAME = 'is-active';
const IS_HIDDEN_CLASS_NAME = 'is-hidden';

/**
 * A tab strip that will enable tab navigation to any content
 */
@customElement('va-tab')
export class VaTab extends LitElement {
    /**
     * Element that wraps all tab nav items
     */
    @queryAssignedNodes('tab-nav-items')
    tabNavItemsSlot!: NodeListOf<HTMLElement>;

    /**
     * Element that wraps all tab panels
     */
    @queryAssignedNodes('tab-panels')
    tabPanelsSlot!: NodeListOf<HTMLElement>;

    @property({ type: Number })
    private selectedIndex: number = -1;

    /**
     * List of tab nav item links
     */
    private tabNavLinks: NodeListOf<HTMLAnchorElement>;

    /**
     * List of tab panels
     */
    private tabPanels: HTMLCollection;

    firstUpdated(): void {
        if (this.tabNavItemsSlot?.length > 0) {
            this.tabNavLinks = this.tabNavItemsSlot[0].querySelectorAll('.tab__nav-link');
            this.tabNavLinks.forEach((link, index) => link.addEventListener('click', (evt: Event) => {
                evt.preventDefault();

                const dataset = (evt.currentTarget as HTMLElement).dataset;
                if (this.showTab(index) && dataset.gtmEvent && dataset.gtmCategory && dataset.gtmAction && dataset.gtmLabel) {
                    GoogleTagManagerService.getInstance().pushEvent(dataset.gtmEvent as GTMEvent, dataset.gtmCategory as GTMCategory, dataset.gtmAction, dataset.gtmLabel);
                }
            }));
        }

        if (this.tabPanelsSlot?.length > 0) {
            this.tabPanels = this.tabPanelsSlot[0].children;
            [...this.tabPanels].forEach(p => p.classList.add(IS_HIDDEN_CLASS_NAME));
        }

        if (this.tabNavLinks?.length > 0) {
            this.showTab(this.selectedIndex);
        }
    }

    /**
     * Shows tab based on provided tab index
     * @param index - Tab index
     * @returns Returns 'true' if tab changed, false otherwise
     */
    showTab(index: number): boolean {
        if (index < 0) {
            index = 0;
        } else if (index > this.tabNavLinks.length) {
            index = this.tabNavLinks.length - 1;
        }

        if (this.selectedIndex !== index) {
            this.tabNavLinks[this.selectedIndex]?.classList?.remove(IS_ACTIVE_CLASS_NAME);
            this.tabPanels[this.selectedIndex]?.classList?.add(IS_HIDDEN_CLASS_NAME);

            this.tabNavLinks[index].classList?.add(IS_ACTIVE_CLASS_NAME);
            this.tabPanels[index]?.classList?.remove(IS_HIDDEN_CLASS_NAME);

            this.selectedIndex = index;

            return true;
        }

        return false;
    }

    /**
     * @override
     */
    render() {
        return html`<section>
            <nav><slot name="tab-nav-items"/></nav>
            <slot name="tab-panels"/>
        </section>`;
    }
}
