import { classNames } from "../constants/class-names";

/**
 * Function to watch window scroll and add/remove sticky class
 * @export
 */
let productElement: HTMLElement;
export function initProductButtonStickToBottom(): void {
    productElement = document.querySelector<HTMLElement>('.js-sticky-action-buttons');
    if (productElement) {
        const topTargetElement = document.querySelector<HTMLElement>(`.${productElement.dataset.rel}`);
        const footerElement = document.querySelector<HTMLElement>('footer');

        if (topTargetElement && footerElement) {
            const elementsToObserve = [topTargetElement, footerElement];
            createObserver(elementsToObserve);
        }
    }
}
/**
 * Function to create an observer to the given targets
 * @param targets array of HTMLElements
 */
function createObserver(targets: Array<HTMLElement>) {
    let observer = new IntersectionObserver(handleIntersect);
    targets.forEach(target => {
        observer.observe(target);
    });
}

/**
 * Function to handle to each time observer is triggered
 * @param entries array of IntersectionObserverEntry entries
 */
function handleIntersect(entries: Array<IntersectionObserverEntry>): void {
    const isVisibleEntries = entries.some( entry => entry.isIntersecting === true);
    productElement.classList.toggle(classNames.isStickyBottom, !isVisibleEntries);
    productElement.classList.toggle(classNames.displayNone, isVisibleEntries);
}