import { customElement, LitElement, html, css, unsafeCSS, property, TemplateResult } from 'lit-element';
import StyleBase from './slideshow-indicator-base.scss';

export const COMPONENT_NAME = 'va-slideshow-indicator';

@customElement(COMPONENT_NAME)
export class SlideshowIndicator extends LitElement {

    @property({ type: Boolean })
    public isActive = false;

    /**
     * Generates the base html for a slideshow indicator. The base may be extended, based in extra CSS classes and/or content
     * @param isActive - The flag that indicates if the indicator is the currently active
     * @param extraCssClasses - A space separated string, containing the additional CSS classes for the base
     * @param contentHtml - The additional content inside the base template
     */
    public baseHtml(isActive: boolean, extraCssClasses: string = '', contentHtml: TemplateResult = html``): TemplateResult {
        return html`
            <li class="carousel__indicator-item ${extraCssClasses} ${isActive ? 'is-active' : ''}">
                ${contentHtml}
            </li>`;
    }

    render(): TemplateResult {
        return html`${this.baseHtml(this.isActive)}`;
    }

    static get styles() {
        return [css`${unsafeCSS(StyleBase)}`];
    }
}
