import Style from './va-flip.scss';
import {
    css,
    customElement,
    html,
    LitElement,
    property,
    unsafeCSS,
    queryAssignedNodes,
} from 'lit-element';

/**
 * The tagname of the current component
 */
export const COMPONENT_NAME = 'va-flip';

/**
 * A flip view is an element which can display 2 different panel and where
 * we switch from one to the other.
 */
@customElement(COMPONENT_NAME)
export class VaFlip extends LitElement {
    /**
     * This property manage the flip of recto vs verso
     */
    @property({ type: Boolean })
    isFlipped = false;

    /**
     * The height to set to the flip wrapper so that all the content is visible
     */
    @property({ type: Number, attribute: false })
    contentHeight = 0;

    /**
     * Inform whether the animation is a degraded one because the browser is from jurassic
     */
    @property({ attribute: false })
    isDegraded = false;

    /**
     * The node related to the recto slot
     */
    @queryAssignedNodes('recto', true)
    _rectoNodes: NodeListOf<HTMLElement>;

    /**
     * The node related to the verso slot
     */
    @queryAssignedNodes('verso', true)
    _versoNodes: NodeListOf<HTMLElement>;

    /**
     * Webcomponent lifecycle method
     * @see https://lit-element.polymer-project.org/guide/events#add-event-listners-in-connectedcallback
     */
    connectedCallback(): void {
        super.connectedCallback();
        window.addEventListener('resize', this.updateContainerSize.bind(this));
        this.detectIE();
    }

    /**
     * Webcomponent lifecycle method
     * @see https://lit-element.polymer-project.org/guide/events#add-event-listners-in-connectedcallback
     */
    disconnectedCallback(): void {
        super.disconnectedCallback();
        window.removeEventListener('resize', this.updateContainerSize);
    }

    /**
     * Lit-element lifecycle method
     * @see https://lit-element.polymer-project.org/guide/lifecycle#updated
     */
    async updated() {
        this.updateContainerSize();
    }

    /**
     * method automatically triggered by lit-element when the passed slot change
     * @see https://lit-element.polymer-project.org/guide/templates#accessing-slotted-children
     * @param _evt - the slot change event
     */
    handleSlotchange(_evt: Event): void {
        this.updateContainerSize();
    }

    /**
     * Check if internet explorer to cancel the unsupported flip-effect
     * Usually we should not detect browser but features support, however here we don't know what's its problem...
     */
    detectIE(): void {
        const msie = window.navigator.userAgent.indexOf('MSIE ');
        this.isDegraded = !!(msie > 0 || !!navigator.userAgent.match(/Trident.*rv\:11\./));
    }

    /**
     * Toggle the display of the current visible view
     * @param shouldFlip - force the toggle to a specific value;
     */
    flip(shouldFlip?: boolean): void {
        if (typeof shouldFlip !== 'boolean') {
            shouldFlip = !this.isFlipped;
        }

        this.isFlipped = shouldFlip;
    }

    /**
     * Defines the height of the wrapper element according to the biggest slot's height
     */
    updateContainerSize() {
        const maxHeight = Math.max(
            this._getMaxHeightOfSlot(this._rectoNodes),
            this._getMaxHeightOfSlot(this._versoNodes),
        );
        this.contentHeight = maxHeight;
    }

    /**
     * Calculate the height required by the slot
     * @param nodes - the nodes in the slot
     * @returns the calculated height
     */
    _getMaxHeightOfSlot(nodes: NodeListOf<Node>): number {
        let maxHeight = 0;
        nodes.forEach((slot: HTMLSlotElement) => {
            const slotHeight = slot.offsetHeight;
            maxHeight += slotHeight;
        });

        return maxHeight;
    }

    /**
     * This method generate the height to set in the style tag. If we are on
     * Internet explorer, we don't set any height as the element is relatively positioned
     * @returns the declaration to set in the style element
     */
    getHeightStyleForTemplate(): string {
        if (this.isDegraded) {
            return '';
        }

        return `height:${this.contentHeight}px`;
    }

    /**
     * @override
     */
    render() {
        return html`
            <div class="va-flip-wrapper ${this.isFlipped ? 'is-flipped' : ''} ${this.isDegraded ? 'is-internet-explorer' : '' }"
                style="${this.getHeightStyleForTemplate()}">
                <div class="va-flip">
                    <section class="va-flip__recto" style="${this.isDegraded && this.isFlipped ? 'display: none' : ''}">
                        <slot name="recto"></slot>
                    </section>
                    <section class="va-flip__verso" style="${this.isDegraded && !this.isFlipped ? 'display: none' : ''}">
                        <slot name="verso"></slot>
                    </section>
                </div>
            </div>
        `;
    }

    /**
     * Css styles
     * @returns the CSS styles
     */
    static get styles() {
        return [css`${unsafeCSS(Style)}`];
    }
}
